import {
  Component,
  ContentChild,
  HostBinding,
  ElementRef,
  Input,
  ViewChild,
  Output,
  EventEmitter,
  OnInit,
  AfterViewInit
} from '@angular/core';
import { ContextMenuComponent } from '../context-menu/context-menu.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';

@Component({
  selector: 'bmw-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.less']
})
export class ListItemComponent implements OnInit, AfterViewInit {
  @ContentChild(ContextMenuComponent)
  childContextMenuComponent: ContextMenuComponent;
  @ViewChild('contextMenu')
  contextMenu: ElementRef;
  @ViewChild('labelElement')
  labelElement: ElementRef;
  @ViewChild('checkboxElement', { static: false })
  checkboxElement: ElementRef;
  @HostBinding('attr.id')
  @Input()
  id: string;
  @Input()
  selected: boolean;
  @Input()
  icon: string;
  @Input()
  multiSelect = false;
  @Input()
  label: string;
  @HostBinding('style.order')
  @Input()
  order: string;
  @Output()
  updateEvent = new EventEmitter<boolean>();
  withContextMenu = false;

  ngOnInit() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);
  }

  ngAfterViewInit() {
    if (this.contextMenu.nativeElement.firstChild) {
      // wait for next tick to prevent expresionChangedAfterCheck error
      setTimeout(() => {
        this.withContextMenu = true;
      });
    }
  }
  focusOut(e: MouseEvent) {
    const leavingParent = !this.contextMenu.nativeElement.contains(e.relatedTarget);
    if (leavingParent) {
      this.toggle(false);
    }
  }

  openContextMenu(e: MouseEvent) {
    this.toggle();
    e.stopPropagation();
  }

  toggle(state?: boolean) {
    if (!this.childContextMenuComponent) return;
    this.childContextMenuComponent.toggle(state);
  }

  checkboxUpdate(event: CheckboxComponent) {
    if ((event.checked === 'checked') === this.selected) return;

    this.updateEvent.emit(event.checked === 'checked');
  }

  click(event: MouseEvent) {
    //ignore clicks inside checkbox
    if (event.target !== this.labelElement.nativeElement) return;

    this.updateEvent.emit(this.selected);
  }
}
