import {
  Component,
  ElementRef,
  Input,
  ViewChild,
  HostBinding,
  SimpleChanges,
  Output,
  EventEmitter,
  OnChanges
} from '@angular/core';

@Component({
  selector: 'bmw-content-tab-label',
  templateUrl: './content-tab-label.component.html',
  styleUrls: ['./content-tab-label.component.less']
})
export class ContentTabLabelComponent implements OnChanges {
  // The CSS class of an icon.
  // Note that the icon won't be shown unless the withIcon flag is set to true on the parent component.
  @Input()
  icon: string;

  // The id of the bmw-content-tab-content component for this label.
  @Input()
  for: string;

  @Output() change: EventEmitter<SimpleChanges> = new EventEmitter<SimpleChanges>();

  // Boolean flag to pre-select this label and the respective content.
  // If multiple tabs per tabset have the selected input set to true, the last one of them will be selected.
  @HostBinding('attr.aria-expanded')
  @HostBinding('attr.aria-selected')
  @Input()
  selected = false;

  @HostBinding('attr.aria-disabled')
  @Input()
  disabled = false;

  @ViewChild('label')
  label: ElementRef;

  @HostBinding('attr.role')
  role = 'tab';

  constructor(public elementRef: ElementRef) {}

  ngOnChanges(): void {
    this.change.emit();
  }
}
