import { Component, EventEmitter, HostBinding, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'bmw-dropdown-item',
  templateUrl: './dropdown-item.component.html',
  styleUrls: ['./dropdown-item.component.less']
})
export class DropdownItemComponent implements OnInit {
  @Input()
  disabled = false;
  selected: boolean;
  @Input()
  value: any;
  @Input()
  label: string;
  @Input()
  id: string;
  @Output()
  clicked = new EventEmitter<{ id: string; value: any }>();
  @HostBinding('class.keyboard-focus')
  keyboardFocus = false;

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  click() {
    if (!this.disabled) this.clicked.emit({ id: this.id, value: this.value });
  }
}
