import {
  AfterContentInit,
  Component,
  ContentChildren,
  EventEmitter,
  forwardRef,
  HostBinding,
  HostListener,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ElementRef,
  isDevMode
} from '@angular/core';
import { DropdownItemComponent } from './dropdown-item.component';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'bmw-dropdown',
  templateUrl: './dropdown.component.html',
  styleUrls: ['./dropdown.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => DropdownComponent)
    }
  ]
})
export class DropdownComponent implements OnInit, AfterContentInit, OnDestroy, ControlValueAccessor {
  // reference to the selected dropdown-item
  // used to show the correct label when a reset option is used
  selectedItem: DropdownItemComponent;

  @ContentChildren(DropdownItemComponent)
  childDropdownMenuItems: QueryList<DropdownItemComponent>;
  @Input()
  disabled: boolean;
  @Input()
  required: boolean;
  @Input()
  valid: boolean;
  @HostBinding('attr.id')
  @Input()
  id: string;
  @Output()
  valueChange = new EventEmitter<any>();
  isValid: boolean;
  opened = false;
  label: string;
  gridClasses: string[];
  private _value: any;
  private _hovered: number;
  // this is emitted in ngOnDestroy
  private readonly _destroy = new Subject<void>();
  private _onChange: (value: any) => void = () => {};
  private _onTouched: () => any = () => {};

  get value(): any {
    return this._value;
  }

  @Input()
  set value(newValue: any) {
    if (newValue !== this._value) {
      this._value = newValue;
      this._onChange(newValue);
      if (this.childDropdownMenuItems) {
        this.childDropdownMenuItems.forEach(item => {
          if (this._value === item.value) {
            item.selected = true;
            this.label = item.label;
            this.selectedItem = item;
          } else {
            item.selected = false;
          }
        });
      }
    }
  }

  constructor(private _el: ElementRef) {}

  ngOnInit() {
    if (isDevMode()) {
      console.warn('Warning - <bmw-dropdown> component is deprecated and will be removed in the near future.');
      console.warn('Please use <bmw-combobox> instead https://designsystem.bmwgroup.net/components/form-combo-box');
    }

    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  ngAfterContentInit() {
    // do this in the next cycle to avoid issues with dynamic component creation
    // TODO refactor this hack: call change detection when needed
    setTimeout(() => {
      this._prepareItems();
      this._validate();

      this.childDropdownMenuItems.changes.pipe(takeUntil(this._destroy)).subscribe(() => {
        this._prepareItems();
      });
      this._setGridClasses();
    });
  }

  ngOnDestroy(): void {
    this._destroy.next();
    this._destroy.complete();
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event) {
    const keyName = event.key;
    event.preventDefault();
    if (keyName.slice(-4) === 'Down') {
      this.toggle(true);
      if (this._hovered === this.childDropdownMenuItems.length - 1) return;
      typeof this._hovered !== 'undefined' ? this._hovered++ : (this._hovered = 0);
      this._hover(this._hovered);
    } else if (keyName.slice(-2) === 'Up') {
      this.toggle(true);
      if (this._hovered === 0) return;
      typeof this._hovered !== 'undefined' ? this._hovered-- : (this._hovered = this.childDropdownMenuItems.length - 1);
      this._hover(this._hovered);
    } else if (keyName === 'Enter') {
      const item = this.childDropdownMenuItems[this._hovered];
      if (!item.disabled) {
        this._select(item.value);
        this.toggle(false);
      }
    } else if (keyName.slice(0, 3) === 'Esc') {
      this.toggle(false);
    }
  }

  @HostListener('document:click', ['$event.target'])
  onClickOutside($e) {
    if ($e && !this._el.nativeElement.contains($e)) {
      this.toggle(false);
    }
  }

  private _prepareItems() {
    this.childDropdownMenuItems.forEach(item => {
      if (this._value === item.value) {
        this._select(item.value);
      }

      if (item.clicked.observers.length > 0) {
        return;
      }

      item.clicked.pipe(takeUntil(this._destroy)).subscribe(selectedItem => {
        this._select(selectedItem.value);
      });
    });
  }

  click(event: MouseEvent) {
    this.toggle();
  }

  blur() {
    this._onTouched();
  }

  toggle(state?: boolean) {
    if (typeof state !== 'undefined') {
      this.opened = state;
    } else {
      this.opened = !this.opened;
    }
  }

  private _validate() {
    if (typeof this.valid !== 'undefined' && this.valid !== null) {
      this.isValid = this.valid;
    } else {
      setTimeout(() => {
        const invalid = Array.from(this._el.nativeElement.classList).includes('ng-invalid');

        this.isValid = (this.required !== true || this._value) && !invalid;
      });
    }
  }

  private _select(value: string) {
    this.value = value;
    this.childDropdownMenuItems.forEach(item => {
      if (item.value === value) {
        item.selected = true;
        this.label = item.label;
        this.selectedItem = item;
      } else {
        item.selected = false;
      }
    });
    this.valueChange.emit(value);
    this._validate();
  }

  private _hover(selectedItemIndex) {
    this.childDropdownMenuItems.forEach((item, index) => {
      if (index === selectedItemIndex) {
        item.keyboardFocus = true;
      } else {
        item.keyboardFocus = false;
      }
    });
  }

  private _setGridClasses() {
    const listOfClasses = Array.from((this._el.nativeElement as HTMLElement).classList);
    this.gridClasses = listOfClasses.filter((item: string) => item.match(/bmw__grid__cell--.*/));
    if (this.gridClasses.length) {
      const wrapperClassList = this._el.nativeElement.querySelector('div.dropdown-outer-wrapper').classList;
      wrapperClassList.value.split(' ').forEach((className: string) => {
        if (className.match(/bmw__grid__cell--.*/)) {
          wrapperClassList.remove(className);
        }
      });
      this.gridClasses.forEach((className: string) => {
        this._el.nativeElement.querySelector('div.dropdown-outer-wrapper').classList.add(className);
        this._el.nativeElement.classList.remove(className);
      });
    }
  }

  registerOnChange(fn: (value: any) => void) {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  writeValue(value: any): void {
    this.value = value;
  }
}
