import { BaseInput } from './base-input';
import {
  AfterContentInit,
  ChangeDetectorRef,
  Component,
  ContentChild,
  HostListener,
  Input,
  OnInit,
  Output,
  EventEmitter,
  ViewChild,
  OnDestroy
} from '@angular/core';
import { BmwInputDirective } from './bmw-input.directive';
import { LabelComponent } from '../label/label.component';

@Component({
  selector: 'bmw-inputfield',
  templateUrl: './inputfield.component.html',
  styleUrls: ['./inputfield.component.less']
})
export class InputfieldComponent implements OnInit, AfterContentInit, OnDestroy {
  @Input()
  icon: string;
  @Input()
  id: string;
  mutationObserver: MutationObserver;
  filePlaceholder: any = '';
  @Input()
  set disabled(value: boolean) {
    this._disabled = value;
    if (this._label) {
      this._label.disabled = value;
    }
    this.cdRef.detectChanges();
  }
  get disabled() {
    return this._disabled;
  }
  @Output()
  iconClicked: EventEmitter<any> = new EventEmitter();
  type: string;

  /**
   * Please leave it undefined and don't initialize it with false
   * as the bmwInput directive needs that information
   * to work correctly or you run into the problem that the inputfield wants to read
   * from the bmwInput and vice versa
   */
  _disabled: boolean;
  @ContentChild(BaseInput, { static: true })
  public input;

  @Input()
  preventSettingIDAutomatically = false;

  @ContentChild(LabelComponent, { static: true })
  private _label: LabelComponent;

  constructor(private cdRef: ChangeDetectorRef) {}

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  iconClick() {
    this.iconClicked.emit(this);
  }

  ngAfterContentInit() {
    if (!this.input) {
      throw new Error('The inputfield must contain an element with the bmwInput directive.');
    }
    if (!this.preventSettingIDAutomatically) {
      const inputId = 'bmw-input-' + this.id;
      this.input.withIcon = this.icon && this.icon.length > 0;
      this.input.id = inputId;
      if (this._label) {
        this._label.inputId = this.input.id;
      }
    }
    this.type = this.input.type;
    // explicitly call change detection to avoid issues with dynamic component creation
    this.cdRef.detectChanges();
    this.disabled = this.input.disabled;

    this.input.disableToggle.subscribe((value: boolean) => {
      this.disabled = value;
    });

    window.MutationObserver = window.MutationObserver;
    this.filePlaceholder = this.input?.placeholder || this.input._el.nativeElement.placeholder;
    this.mutationObserver = new MutationObserver(mutationRecord => {
      const placeholderMutation: MutationRecord = mutationRecord.find(
        mutation => mutation.attributeName === 'placeholder'
      );
      if (placeholderMutation) {
        this.filePlaceholder = (placeholderMutation.target as any).placeholder;
      }
    });
    this.mutationObserver.observe(this.input._el.nativeElement, { attributes: true });
  }

  @HostListener('input')
  _onInput() {
    // this is required in order to be notified about changes to the native input element
  }

  ngOnDestroy() {
    this.mutationObserver?.disconnect();
  }
}
