import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  HostBinding,
  HostListener,
  ElementRef
} from '@angular/core';

@Component({
  selector: 'bmw-generic-tile',
  templateUrl: './generic-tile.component.html',
  styleUrls: ['./generic-tile.component.less']
})
export class GenericTileComponent implements OnInit, OnChanges {
  @Input()
  interaction: string;
  @Input()
  selected = false;
  @HostBinding('attr.id')
  @Input()
  id: string;
  @Output()
  clickEvent = new EventEmitter<any>();
  clickable = false;
  selectable = false;

  @Input() disabled = false;

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        if (event.target === this._el.nativeElement.querySelector('.generic-tile')) {
          this.click();
          event.preventDefault();
        }
        break;
    }
  }

  constructor(private _el: ElementRef) {}

  ngOnInit() {
    this.checkProperties();
  }

  ngOnChanges() {
    this.checkProperties();
  }

  checkProperties() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }

    this.clickable = this.interaction === 'clickable';
    this.selectable = this.interaction === 'selectable';
  }

  click() {
    if (this.disabled || (!this.clickable && !this.selectable)) return;

    if (this.selectable) {
      this.selected = !this.selected;
    }

    this.clickEvent.emit({ interaction: this.interaction, selected: this.selected, id: this.id });
  }
}
