import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  HostBinding,
  OnChanges,
  HostListener,
  ElementRef
} from '@angular/core';

const SUPPORTED_TYPES = ['info', 'success', 'warning', 'error'];

@Component({
  selector: 'bmw-inline-message',
  templateUrl: './inline-message.component.html',
  styleUrls: ['./inline-message.component.less']
})
export class InlineMessageComponent implements OnInit, OnChanges {
  protected _type = '';
  protected _messages = [];
  protected _showCloseButton = true;

  get type() {
    return this._type;
  }

  get messages() {
    return this._messages;
  }

  get showCloseButton() {
    return this._showCloseButton;
  }

  @Input()
  set type(value: string) {
    if (SUPPORTED_TYPES.indexOf(value) === -1) {
      console.warn(`Invalid or unsupported type: ${value}`);
      this._type = 'info';
    } else {
      this._type = value;
    }
  }

  @Input()
  set messages(value: string | string[]) {
    if (typeof value === 'string') {
      this._messages = [value];
    } else {
      this._messages = value;
    }
  }

  @Input()
  set showCloseButton(value: boolean) {
    if (value === false) {
      this._showCloseButton = value;
    }
  }

  @Input() groupHeadline: string;

  @HostBinding('attr.id')
  @Input()
  id: string;

  @Output()
  closeEvent = new EventEmitter<any>();

  closed = false;

  constructor(private _el: ElementRef) {}

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        if (event.target === this._el.nativeElement.querySelector('.close')) {
          this.close();
          event.preventDefault();
        }
        break;
    }
  }

  ngOnInit() {
    this.checkProperties();
  }

  ngOnChanges() {
    this.checkProperties();
  }

  checkProperties() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }
  close() {
    this.closeEvent.emit();
    this.closed = true;
  }
}
