import { Component, EventEmitter, HostBinding, Input, Output, ViewChild, ElementRef, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-combo-box-dropdown-checkbox-item',
  templateUrl: './combo-box-dropdown-checkbox-item.component.html',
  styleUrls: ['./combo-box-dropdown-checkbox-item.component.less']
})
export class ComboBoxDropdownCheckboxItemComponent implements OnChanges {
  @Input()
  value: any;
  @Input()
  label: string;
  @Input()
  disabled = false;
  @Input()
  selected = false;
  @Input()
  focused = false;

  @ViewChild('itemWrapper')
  itemWrapper: ElementRef;

  @HostBinding('attr.id')
  @Input()
  id = Math.random()
    .toString(36)
    .substring(2);

  @Output()
  selectedChange = new EventEmitter<boolean>();

  ngOnChanges(): void {
    if (this.focused) {
      this.itemWrapper?.nativeElement.focus();
    }
  }

  click() {
    if (!this.disabled) {
      this.selectedChange.emit(!this.selected);
    }
  }
}
