import { ToggleButtonComponent } from './../../toggle-button/toggle-button.component';
import {
  Component,
  Output,
  Input,
  EventEmitter,
  ViewChild,
  TemplateRef,
  ElementRef,
  OnInit,
  ChangeDetectorRef
} from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'bmw-segmented-button-item',
  templateUrl: './segmented-button-item.component.html',
  styleUrls: ['./segmented-button-item.component.less']
})
export class SegmentedButtonItemComponent implements OnInit {
  @ViewChild('template', { read: TemplateRef })
  sample: TemplateRef<any>;

  @ViewChild(ToggleButtonComponent)
  button: ToggleButtonComponent;

  @Input()
  disabled: boolean;

  @Input()
  checked: boolean;

  @Input()
  value: string;

  @Input()
  id: string;

  @Output()
  buttonClicked: EventEmitter<SegmentedButtonItemComponent> = new EventEmitter();

  focused = false;
  isInDropdown = false;
  fromTop: string;
  itemWidth = 'auto';
  isHidden: boolean;
  native: HTMLElement;
  reactiveForm: FormGroup;

  constructor(private el: ElementRef, private cd: ChangeDetectorRef) {
    this.native = this.el.nativeElement;
  }

  ngOnInit(): void {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);

    if (!this.value) this.value = this.id;

    const controlObject = {};
    controlObject[this.id] = new FormControl(this.checked);
    this.reactiveForm = new FormGroup(controlObject);
  }

  toggleItemVisibility(state?: boolean) {
    if (typeof state === 'undefined') {
      this.isHidden = !this.isHidden;
    } else {
      this.isHidden = state;
    }
  }

  setChecked(state?: boolean) {
    setTimeout(() => {
      if (this.disabled) return;
      if (typeof state === 'undefined') {
        this.checked = !this.checked;
      } else {
        this.checked = state;
      }
      this.reactiveForm.controls[this.id].setValue(this.checked);
    });
  }

  setTop(top: number) {
    this.fromTop = `calc(${top * 100}% - ${top}px)`;
  }

  setWidth(width: number) {
    this.itemWidth = `${width}px`;
  }

  moveToDropdown() {
    this.isInDropdown = true;
  }

  removeFromDropdown() {
    this.itemWidth = 'auto';
    this.isInDropdown = false;
  }

  resetVisibility() {
    this.removeFromDropdown();
    this.toggleItemVisibility(false);
  }

  onClick(event: any) {
    if (this.disabled) return;
    this.buttonClicked.emit(this);
  }
}
