import {
  Component,
  Input,
  ViewContainerRef,
  ViewChild,
  ContentChild,
  HostBinding,
  AfterViewInit,
  EventEmitter,
  Output,
  HostListener,
  ElementRef
} from '@angular/core';
import { ContextMenuComponent, ContextMenuItemComponent } from '../context-menu/context-menu.component';

@Component({
  selector: 'bmw-split-button',
  templateUrl: './split-button.component.html',
  styleUrls: ['./split-button.component.less']
})
export class SplitButtonComponent implements AfterViewInit {
  @ContentChild(ContextMenuComponent)
  childContextMenu: ContextMenuComponent;
  @ViewChild('contextMenu', { read: ViewContainerRef })
  contextMenu: ViewContainerRef;
  @Input()
  iconClass: string;
  @Input()
  disabledButton: boolean;
  @Input()
  disabledMenu: boolean;
  @Output()
  itemSelected: EventEmitter<ContextMenuItemComponent> = new EventEmitter<ContextMenuItemComponent>();
  @Output()
  clickEvent = new EventEmitter<SplitButtonComponent>();

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 27:
      case 'Space':
      case 32:
        if (
          event.target === this.elemRef.nativeElement.querySelector('.button-arrow') ||
          event.target === this.elemRef.nativeElement.querySelector('.icon-arrow')
        ) {
          this.toggle();
          event.preventDefault();
          event.stopPropagation();
        }
        break;
    }
  }

  @HostListener('document:click', ['$event'])
  clickout(event: KeyboardEvent) {
    if (!this.contextMenu.element.nativeElement.contains(event.target)) {
      this.toggle(false);
    }
    event.stopPropagation();
  }

  constructor(private elemRef: ElementRef) {}

  ngAfterViewInit() {
    this.childContextMenu.childContextMenuItems.forEach(item => {
      this.assignClick(item);
    });
  }

  // Recursevly shake all menu items and emit item onClick and close menu
  assignClick(menuItem: ContextMenuItemComponent) {
    if (menuItem.subMenu) {
      menuItem.subMenu.childContextMenuItems.forEach((item: ContextMenuItemComponent) => {
        this.assignClick(item);
      });
      return;
    }
    menuItem.selected.subscribe((item: ContextMenuItemComponent) => {
      this.itemSelected.emit(item);
      this.toggle(false);
    });
  }

  arrowButtonClick(event: MouseEvent) {
    this.toggle();
    event?.stopPropagation();
  }

  buttonClick() {
    if (this.disabledButton) return;
    this.clickEvent.emit(this);
  }

  toggle(state?: boolean) {
    if (!this.childContextMenu || !this.childContextMenu.toggle) return;

    this.childContextMenu.unselectSubs();
    this.childContextMenu.toggle(state);
  }

  @HostBinding('class.opened')
  get focus() {
    return this.childContextMenu ? this.childContextMenu.opened : false;
  }
}
