import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'bmw-navigation-menu-submenu',
  templateUrl: './navigation-menu-submenu.component.html',
  styleUrls: ['./navigation-menu-submenu.component.less']
})
export class NavigationMenuSubmenuComponent implements OnInit {
  @Input()
  label: string;
  opened = false;
  toggleMenu($event) {
    $event.stopPropagation();
    this.opened = !this.opened;
  }

  ngOnInit() {
    this.opened = this.label ? false : true;
  }
}
