import { Component, ViewEncapsulation, Input, HostBinding, OnInit, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-breadcrumb-item',
  templateUrl: './breadcrumb-item.component.html',
  styleUrls: ['./breadcrumb-item.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class BreadcrumbItemComponent implements OnInit, OnChanges {
  @HostBinding('attr.id')
  @Input()
  id: string;

  ngOnInit() {
    this.checkProperties();
  }

  ngOnChanges() {
    this.checkProperties();
  }

  checkProperties() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }
}
