import {
  Component,
  OnInit,
  Input,
  ElementRef,
  EventEmitter,
  Output,
  OnChanges,
  forwardRef,
  HostListener,
  ViewChild,
  HostBinding
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormGroup, FormControl, FormBuilder } from '@angular/forms';

@Component({
  selector: 'bmw-toggle-button',
  templateUrl: './toggle-button.component.html',
  styleUrls: ['./toggle-button.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ToggleButtonComponent),
      multi: true
    }
  ]
})
export class ToggleButtonComponent implements ControlValueAccessor, OnInit, OnChanges {
  @ViewChild('buttonInput')
  buttonInput: ElementRef;
  @Input()
  name: string;
  @Input()
  iconRight: string;
  @Input()
  iconLeft: string;
  @Input()
  disabled = false;
  @Input()
  checked = false;
  @Input()
  toggleButton: string;
  @Input()
  form: FormGroup;
  @Input()
  id: string;
  @Input()
  tabIndex = 0;
  @Output()
  updateEvent = new EventEmitter<ToggleButtonComponent>();
  primary = false;
  secondary = false;
  small = false;

  private onChange: (value: any) => void = () => {};
  private onTouched: () => any = () => {};

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        if (!this.disabled) {
          this.change();
          this.form.controls[this.name].setValue(this.checked);
          event.preventDefault();
        }
        break;
    }
  }

  constructor() {}

  writeValue(value: boolean): void {
    this.checked = value;
  }

  registerOnChange(fn: (_: boolean) => void): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  ngOnInit() {
    this.generateIfNecessary();
    this.checkProperties();
  }

  ngOnChanges() {
    this.generateIfNecessary();
    this.checkProperties();
  }

  checkProperties() {
    if (this.toggleButton) {
      const properties = this.toggleButton.split(' ');
      if (properties.indexOf('primary') > -1) {
        this.primary = true;
      } else if (properties.indexOf('secondary') > -1) {
        this.secondary = true;
      }
      if (properties.indexOf('small') > -1) {
        this.small = true;
      }
    }

    if (this.iconLeft) {
      this.iconRight = undefined;
    }

    if (this.disabled) {
      this.form.controls[this.name].disable();
    } else {
      this.form.controls[this.name].enable();
    }
  }

  generateIfNecessary() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);

    if (!this.name) this.name = 'toggle-button-' + this.id;

    if (!this.form) {
      const control = {};
      control[this.name] = new FormControl(this.checked);
      this.form = new FormBuilder().group(control);
    }
  }

  change() {
    this.checked = !this.checked;
    this.updateEvent.emit(this);

    this.onChange(this.checked);
    if (this.onTouched) {
      this.onTouched();
    }
  }

  focus() {
    this.buttonInput.nativeElement.focus();
  }

  stopPropagation(event: MouseEvent) {
    event.stopPropagation();
  }
}
