import {
  Component,
  AfterViewInit,
  Input,
  ViewChild,
  ElementRef,
  EventEmitter,
  Output,
  OnChanges,
  HostListener,
  Optional,
  Self
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';

export const enum CheckboxState {
  checked = 'checked',
  indeterminate = 'indeterminate',
  unchecked = 'unchecked'
}
@Component({
  selector: 'bmw-checkbox',
  templateUrl: './checkbox.component.html',
  styleUrls: ['./checkbox.component.less']
})
export class CheckboxComponent implements ControlValueAccessor, AfterViewInit, OnChanges {
  @ViewChild('input')
  input: ElementRef;
  @Input()
  name: string;
  @Input()
  disabled = false;
  @Input()
  checked: CheckboxState = CheckboxState.unchecked;
  @Input()
  required = false;
  @Input()
  id: string;

  @Output()
  updateEvent = new EventEmitter<CheckboxComponent>();

  invalid: boolean;
  private isDirty = false;
  private isTouched = false;

  private onChange: (value: any) => void = () => {};
  private onTouched: () => any = () => {};

  @HostListener('keydown', ['$event'])
  onKeyDown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Space':
      case 32:
        if (!this.disabled) {
          this.input.nativeElement.indeterminate = false;
          this.input.nativeElement.checked = !this.input.nativeElement.checked;
          this.readState();
          event.preventDefault();
        }
        break;
    }
  }

  constructor(@Self() @Optional() private control: NgControl, public elementRef: ElementRef) {
    if (this.control) {
      this.control.valueAccessor = this;
    }
  }

  writeValue(value: CheckboxState | boolean): void {
    if (value === true) {
      this.checked = CheckboxState.checked;
    } else if (value === false) {
      this.checked = CheckboxState.unchecked;
    } else {
      this.checked = value;
    }
  }

  registerOnChange(fn: (_: boolean) => void): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  ngAfterViewInit() {
    Promise.resolve().then(() => {
      if (!this.id)
        this.id = Math.random()
          .toString(36)
          .substring(2);
      if (!this.name) this.name = 'checkbox-' + this.id;
      this.checkProperties();
    });
  }

  ngOnChanges() {
    this.checkProperties();
  }

  checkProperties() {
    if (!this.input) return;

    if (this.disabled) {
      this.input.nativeElement.disabled = true;
      this.elementRef.nativeElement.classList.add('disabled');
    } else {
      this.input.nativeElement.disabled = false;
      this.elementRef.nativeElement.classList.remove('disabled');
    }
    this.updateCheckState();
  }

  readState() {
    if (this.input.nativeElement.indeterminate || this.disabled) return;

    const lastState = this.checked;
    this.checked = this.input.nativeElement.checked
      ? CheckboxState.checked
      : this.input.nativeElement.indeterminate
      ? CheckboxState.indeterminate
      : CheckboxState.unchecked;

    this.onChange(this.checked === CheckboxState.checked);

    if (lastState !== this.checked) {
      this.updateEvent.emit(this);
    }

    this.isDirty = true;
    this.isTouched = true;

    if (this.onTouched) {
      this.onTouched();
    }

    if (this.control?.valueAccessor) {
      const { touched, dirty, invalid } = this.control;
      this.invalid = invalid ? dirty || touched : false;
    } else {
      this.invalid =
        (this.isDirty || this.isTouched) && this.required === true && this.checked === CheckboxState.unchecked;
    }
  }

  private updateCheckState() {
    if (!this.input) return;
    if (this.checked === CheckboxState.indeterminate) {
      this.input.nativeElement.indeterminate = true;
      this.input.nativeElement.checked = false;
    } else {
      this.input.nativeElement.checked = this.checked === CheckboxState.checked || false;
      this.input.nativeElement.indeterminate = false;
    }
  }

  stopPropagation(event: MouseEvent) {
    event.stopPropagation();
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
    this.checkProperties();
  }
}
