import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  EventEmitter,
  forwardRef,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  HostListener,
  ElementRef,
  Self
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
import { Subject, Subscription } from 'rxjs';

export interface RadiobuttonChangeEvent {
  source: RadiobuttonComponent | null;
  value: any;
}

// RadiobuttonGroupComponent and RadiobuttonComponent exist in the same file in order to prevent a circular dependency
@Component({
  selector: 'bmw-radiobutton-group',
  template: '<ng-content></ng-content>',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RadiobuttonGroupComponent implements ControlValueAccessor, AfterContentInit, OnDestroy {
  id: string;
  readonly _stateChangeSubject = new Subject<void>();

  private _disabled = false;
  get disabled(): boolean {
    return this._disabled;
  }

  @Input('disabled')
  set disabled(value: boolean) {
    this._disabled = value;
    this._stateChangeSubject.next();
  }

  private _required = false;
  get required(): boolean {
    return this._required;
  }

  @Input()
  set required(value: boolean) {
    this._required = value;
    this._stateChangeSubject.next();
  }

  private _selected: RadiobuttonComponent | null = null;

  private _name: string;
  get name(): string {
    return this._name;
  }

  @Input()
  set name(value: string) {
    this._name = value;
    this._stateChangeSubject.next();
  }

  private _value: any = null;
  get value(): any {
    return this._value;
  }

  @Input()
  set value(newValue: any) {
    if (this._value !== newValue) {
      // make sure this is set first before setting values and checked props on children
      this._value = newValue;
      this._updateSelectedRadioFromValue();
      this._checkSelectedRadioButton();
      this.validate();
    }
  }

  @Output()
  groupValueChange: EventEmitter<RadiobuttonChangeEvent> = new EventEmitter<RadiobuttonChangeEvent>();

  @ContentChildren(
    forwardRef(() => RadiobuttonComponent),
    { descendants: true }
  )
  radios: QueryList<RadiobuttonComponent>;

  invalid: boolean;
  isDirty = false;
  isTouched = false;

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'ArrowDown':
      case 40:
        if (this.value) {
          this._setNextNeighbourRadioButton(event);
        }
        break;
      case 'ArrowUp':
      case 38:
        if (this.value) {
          this._setPreviousNeighbourRadioButton(event);
        }
        break;
      case 'Space':
      case 32:
        if (!this.value) {
          const focusedRadioButton = this.radios.find(
            (radio: RadiobuttonComponent) => radio.elementRef.nativeElement.querySelector('input') === event.target
          );
          focusedRadioButton._onInputChange(event);
        }
        event.preventDefault();
        break;
    }
  }

  constructor(@Self() @Optional() private control: NgControl, private _changeDetectorRef: ChangeDetectorRef) {
    if (this.control) {
      this.control.valueAccessor = this;
    }

    const id = Math.random()
      .toString(36)
      .substring(2);

    this._name = `bmw-radiobutton-group-${id}`;
  }

  ngAfterContentInit() {
    this._updateSelectedRadioFromValue();
    this._checkSelectedRadioButton();
  }

  ngOnDestroy() {
    this._stateChangeSubject.complete();
  }

  writeValue(value: any): void {
    this.value = value;
  }

  registerOnChange(fn: (value: any) => void) {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouched = fn;
  }

  // this is called by child components
  change(value) {
    this.value = value;
    this._onChange(value);
    this.isDirty = true;
    if (this._onTouched) {
      this._onTouched();
    }
    this.isTouched = true;
    this.groupValueChange.emit({ source: this._selected, value: this._value });

    this.validate();
  }

  private validate() {
    if (this.radios) {
      if (this.control?.valueAccessor) {
        const { touched, dirty, invalid } = this.control;
        this.invalid = invalid ? dirty || touched : false;
      } else {
        this.invalid = (this.isDirty || this.isTouched) && this.required && !this._value;
      }

      this.radios.forEach(radio => {
        radio.invalid = this.invalid;
      });
    }
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  private _updateSelectedRadioFromValue(): void {
    const isAlreadySelected = this._selected !== null && this._selected.value === this._value;
    if (this.radios && !isAlreadySelected) {
      this._selected = null;
      this.radios.forEach(radio => {
        radio.checked = this.value === radio.value;
        if (radio.checked) {
          this._selected = radio;
          radio.focusable = true;
        } else if (this.value) {
          radio.focusable = false;
        }
      });
    }
  }

  private _checkSelectedRadioButton() {
    if (this._selected && !this._selected.checked) {
      this._selected.checked = true;
    }
  }

  private _setNextNeighbourRadioButton(event: KeyboardEvent) {
    const radiosArray = this.radios.toArray();
    const currentRadioButtonIndex: number = radiosArray.findIndex(
      (radioButton: RadiobuttonComponent) => radioButton.checked
    );

    if (currentRadioButtonIndex === radiosArray.length - 1) {
      radiosArray[0]._onInputChange(event);
    } else {
      radiosArray[currentRadioButtonIndex + 1]._onInputChange(event);
    }
  }

  private _setPreviousNeighbourRadioButton(event: KeyboardEvent) {
    const radiosArray = this.radios.toArray();
    const currentRadioButtonIndex: number = radiosArray.findIndex(
      (radioButton: RadiobuttonComponent) => radioButton.checked
    );

    if (currentRadioButtonIndex === 0) {
      radiosArray[radiosArray.length - 1]._onInputChange(event);
    } else {
      radiosArray[currentRadioButtonIndex - 1]._onInputChange(event);
    }
  }

  private _onChange: (value: any) => void = () => {};
  private _onTouched: () => any = () => {};
}

@Component({
  selector: 'bmw-radiobutton',
  templateUrl: './radiobutton.component.html',
  styleUrls: ['./radiobutton.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RadiobuttonComponent),
      multi: true
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RadiobuttonComponent implements ControlValueAccessor, OnInit, OnDestroy {
  @Input()
  id: string;
  private _parentChangeSubscription: Subscription;

  private _name: string = null;
  get name(): string {
    return this.radioGroup ? this.radioGroup.name : this._name;
  }

  @Input()
  set name(value: string) {
    if (this._name !== value) {
      this._name = value;
      this._changeDetectorRef.markForCheck();
    }
  }

  private _value: string = null;
  get value(): any {
    return this._value;
  }

  @Input()
  set value(value: any) {
    if (value !== this._value) {
      this._value = value;
      this.onChangeCallback(value);
    }
  }

  private _checked = false;
  get checked(): boolean {
    return this._checked;
  }

  @Input()
  set checked(value: boolean) {
    if (this._checked !== value) {
      this._checked = value;
      this._changeDetectorRef.markForCheck();
    }
  }

  private _disabled = false;
  get disabled(): boolean {
    return this._disabled || (this.radioGroup && this.radioGroup.disabled);
  }

  @Input()
  set disabled(value: boolean) {
    this._disabled = value;
    this._changeDetectorRef.markForCheck();
  }

  private _required = false;
  get required(): boolean {
    return this._required || (this.radioGroup && this.radioGroup.required);
  }

  @Input()
  set required(value: boolean) {
    this._required = value;
    this._changeDetectorRef.markForCheck();
  }

  private _focusable = true;
  get focusable(): boolean {
    return this._focusable;
  }

  set focusable(value: boolean) {
    this._focusable = value;
  }

  @Output()
  valueChange: EventEmitter<RadiobuttonChangeEvent> = new EventEmitter<RadiobuttonChangeEvent>();

  private _invalid = false;
  get invalid(): boolean {
    return this._invalid;
  }

  set invalid(value: boolean) {
    this._invalid = value;
    this._changeDetectorRef.markForCheck();
  }

  constructor(
    @Optional() public radioGroup: RadiobuttonGroupComponent,
    public elementRef: ElementRef,
    private _changeDetectorRef: ChangeDetectorRef
  ) {}

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
    if (this.radioGroup) {
      this.name = this.radioGroup.name;
      this._parentChangeSubscription = this.radioGroup._stateChangeSubject.subscribe(() => {
        this._changeDetectorRef.markForCheck();
      });
    }
  }

  ngOnDestroy() {
    if (this._parentChangeSubscription) {
      this._parentChangeSubscription.unsubscribe();
    }
  }

  writeValue(value: any): void {
    if (value === this._value) {
      this._checked = true;
      this._changeDetectorRef.markForCheck();
    }
  }

  private onChangeCallback = (_: any) => {};

  registerOnChange(onChange: any): void {
    this.onChangeCallback = onChange;
  }

  private onTouchedCallback = () => {};

  registerOnTouched(onTouched: any): void {
    this.onTouchedCallback = onTouched;
  }

  public touch() {
    this.onTouchedCallback();
  }

  _onInputChange(event: Event) {
    event.stopPropagation();
    this._checked = true;
    this.valueChange.emit({ source: this, value: this._value });
    this.onChangeCallback(this.value);

    if (this.radioGroup && this.value !== this.radioGroup.value) {
      this.radioGroup.change(this.value);
    }
  }

  _onInputClick(event: Event) {
    event.stopPropagation();
  }
}
