import { Component, Input, HostBinding, OnInit, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: ['./progress-bar.component.less']
})
export class ProgressBarComponent implements OnInit, OnChanges {
  @HostBinding('attr.id')
  @Input()
  id: string;

  @Input()
  label: string;

  @Input()
  filled = 0;

  @Input()
  duration = 0.5;

  filledStyleObject = {
    width: '0%',
    transition: 'width 0.5s'
  };

  ngOnInit() {
    this.checkProperties();
  }

  ngOnChanges() {
    this.checkProperties();
  }
  checkProperties() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);

    this.filled = Math.max(0, Math.min(100, this.filled));

    this.filledStyleObject = {
      width: (this.filled ? this.filled.toString() : '0') + '%',
      transition: 'width ' + (this.duration ? this.duration.toString() : '0.5') + 's'
    };
  }
}
