import { EventEmitter } from '@angular/core';

/** Used to not run into cyclic dependency issues
 * because inputfield and input directive both import from each other
 */
export class BaseInput {
  disabled: boolean;
  id: string;
  withIcon: boolean;
  type: string;
  disableToggle: EventEmitter<boolean>;
  placeholder: string;
  classes: string[];
}
