import {
  Component,
  Input,
  AfterViewInit,
  ContentChildren,
  QueryList,
  ViewChild,
  ElementRef,
  OnDestroy,
  HostBinding,
  EventEmitter,
  Output,
  ContentChild
} from '@angular/core';
import { ListItemComponent } from './list-item.component';
import { CheckboxState } from '../checkbox/checkbox.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';

@Component({
  selector: 'bmw-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.less']
})
export class ListComponent implements AfterViewInit, OnDestroy {
  @ViewChild(CheckboxComponent)
  input: CheckboxComponent;
  @ViewChild('items')
  items: ElementRef;
  @ViewChild('header')
  header: ElementRef;
  @ViewChild('headerCheckbox')
  headerCheckbox: ElementRef;

  @HostBinding('attr.id')
  @Input()
  id: string;

  @Input()
  disabled: boolean;

  @Input()
  label: string;

  @Input()
  multiSelect: boolean;

  @ContentChildren(ListItemComponent)
  childItemsComponent: QueryList<ListItemComponent>;

  @Input()
  get selectedItem(): ListItemComponent {
    return this._selectedItem;
  }

  set selectedItem(value: ListItemComponent) {
    if (value !== this._selectedItem) {
      this._selectedItem = value;
      this.selectOne(value);
      this.selectedItemChange.emit(value);
    }
  }

  private _selectedItem: ListItemComponent = null;

  @Output()
  selectedItemChange = new EventEmitter<ListItemComponent>();

  selectHeader: CheckboxState = null;
  sortingOrder: boolean;

  @Output()
  updateEvent = new EventEmitter<ListItemComponent[] | ListItemComponent>();

  ngAfterViewInit() {
    this.childItemsComponent.changes.subscribe({
      next: elements => {
        this.childItemsComponent.forEach((element: any) => {
          element.updateEvent.subscribe((event: boolean) => {
            if (!this.multiSelect) {
              this.selectOne(element);
              this.updateEvent.emit(element);
            } else {
              this.selectMore(element);
              this.updateEvent.next(this.childItemsComponent.filter(i => i.selected));
            }
          });

          if (this.multiSelect) {
            //setTimeout used to avoid value changed after check error
            setTimeout(() => {
              element.multiSelect = true;
            });
          }
        });
      }
    });

    //setTimeout used to avoid value changed after check error
    setTimeout(() => {
      if (!this.id) {
        this.id = Math.random()
          .toString(36)
          .substring(2);
      }
    });

    this.childItemsComponent.forEach((element: any) => {
      element.updateEvent.subscribe((event: boolean) => {
        if (!this.multiSelect) {
          this.selectOne(element);
          this.updateEvent.emit(element);
        } else {
          this.selectMore(element);
          this.updateEvent.next(this.childItemsComponent.filter(i => i.selected));
        }
      });

      if (this.multiSelect) {
        //setTimeout used to avoid value changed after check error
        setTimeout(() => {
          element.multiSelect = true;
        });
      }
    });
    if (this.multiSelect) {
      this.selectMore(null);
    }
  }

  selectOne(selectItem: ListItemComponent) {
    if (!this.childItemsComponent) {
      return;
    }
    this.childItemsComponent.forEach(element => {
      element.selected = element === selectItem;
      if (element.selected) {
        this.selectedItem = element;
      }
    });
  }

  selectMore(selectedItem: ListItemComponent) {
    let selectAll = true;
    let unselectAll = true;
    this.childItemsComponent.forEach(element => {
      if (element === selectedItem) {
        element.selected = !element.selected;
      }
      if (element.selected) {
        selectAll = true && selectAll;
        unselectAll = false;
      } else {
        selectAll = false;
        unselectAll = true && unselectAll;
      }
    });

    this.selectHeader = selectAll ? CheckboxState.checked : unselectAll ? null : CheckboxState.indeterminate;
  }

  headerChange(event: CheckboxComponent) {
    this.childItemsComponent.forEach(element => {
      element.selected = event.checked === 'checked';
    });
    this.updateEvent.next(this.childItemsComponent.filter(i => i.selected));
  }

  sort(event: MouseEvent) {
    //ignore clicks on checkbox
    if (event.target !== this.header.nativeElement) {
      return;
    }

    if (this.sortingOrder == null) {
      this.sortingOrder = false;
    }
    this.sortingOrder = !this.sortingOrder;

    const sort = (a: ListItemComponent, b: ListItemComponent) => {
      const aText = a.label;
      const bText = b.label;
      const pureOrder = aText > bText;
      return pureOrder === this.sortingOrder ? 1 : -1;
    };
    const newArray = this.childItemsComponent.toArray().sort(sort);

    newArray.forEach((item, index) => {
      item.order = index.toString();
    });
  }

  ngOnDestroy() {
    this.childItemsComponent.forEach(element => {
      element.updateEvent.unsubscribe();
    });
  }
}
