import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ContextMenuItemComponent } from './context-menu.component';
import { SelectedItemInfo } from './context-menu-event-provider.directive';

@Injectable()
export class ContextMenuEventProviderService {
  private itemSelectedSource = new Subject<SelectedItemInfo>();

  itemSelected$: Observable<SelectedItemInfo> = this.itemSelectedSource.asObservable();

  broadcastItemSelected(item: ContextMenuItemComponent) {
    this.itemSelectedSource.next({ itemId: item.id, leafItem: !item.subMenu });
  }
}
