import {
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  Input,
  OnInit,
  Output,
  QueryList
} from '@angular/core';
import { MasterDetailListRowEntryComponent } from '../master-detail-list-row-entry/master-detail-list-row-entry.component';

@Component({
  selector: 'bmw-master-detail-list-row',
  templateUrl: './master-detail-list-row.component.html',
  styleUrls: ['./master-detail-list-row.component.less']
})
export class MasterDetailListRowComponent implements OnInit {
  @Input() @HostBinding('class.selected') set selected(value: boolean) {
    if (this._selected !== value) {
      this._selected = value;
      if (this._selected) {
        this.updateScrollPosition();
      }
    }
  }

  get selected(): boolean {
    return this._selected;
  }

  @ContentChildren(MasterDetailListRowEntryComponent)
  listEntities: QueryList<MasterDetailListRowEntryComponent>;
  @HostBinding('attr.id')
  @Input()
  id: string;

  @Output()
  updateEvent = new EventEmitter<void>();

  private _selected: boolean;

  constructor(private readonly element: ElementRef) {}

  @HostListener('click', ['$event']) click(event: MouseEvent) {
    this.updateEvent.emit();
  }

  ngOnInit() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);
  }

  updateScrollPosition() {
    this.element.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'nearest' });
  }
}
