import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  HostListener,
  HostBinding,
  OnChanges,
  SimpleChanges,
  ElementRef,
  AfterViewInit
} from '@angular/core';
const SUPPORTED_TYPES = ['info', 'success', 'warning', 'error'];

export interface Nodes {
  element: Element;
  prevTabIndex: string;
}

@Component({
  selector: 'bmw-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.less']
})
export class DialogComponent implements OnInit, OnChanges, AfterViewInit {
  protected _type = '';

  get type() {
    return this._type;
  }

  @Input() allowToOverflow = false;

  @Input()
  set type(value: string) {
    if (SUPPORTED_TYPES.indexOf(value) === -1) {
      console.warn(`Invalid or unsupported type: ${value}`);
      this._type = SUPPORTED_TYPES[0];
    } else {
      this._type = value;
    }
  }
  @HostBinding('attr.id')
  @Input()
  id: string;

  @Input()
  show: boolean;
  @Input()
  title: string;
  @Input()
  primaryLabel: string;
  @Input()
  secondaryLabel: string;
  @Input()
  primaryDisabled: boolean;
  @Input()
  secondaryDisabled: boolean;
  @Input()
  closeButton: boolean;
  @Output()
  primaryClick = new EventEmitter();
  @Output()
  secondaryClick = new EventEmitter();
  @Output()
  closeClick = new EventEmitter();

  private nonModalNodes: Nodes[] = [];

  private invokingElement: HTMLElement;

  @HostListener('body:keydown.esc', ['$event'])
  onEscKeydownHandler(event: KeyboardEvent) {
    if (this.show) {
      this.show = false;
      this.closeClick.emit();
    }
    event.stopPropagation();
  }

  constructor(private elemRef: ElementRef) {}

  ngOnInit() {
    if (!this.type) this.type = SUPPORTED_TYPES[0];
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.show?.currentValue === true) {
      this.invokingElement = document.activeElement as HTMLElement;
      this.applyFocusTrapInDialog();
    } else if (changes.show?.currentValue === false) {
      this.removeFocusTrapFromDialog();
      this.invokingElement?.focus();
    }
  }

  ngAfterViewInit() {
    if (this.show) {
      this.applyFocusTrapInDialog();
    }
  }

  closeButtonClick() {
    if (!this.closeButton) return;
    this.closeClick.emit();
  }
  primaryButtonClick() {
    this.primaryClick.emit();
  }
  secondaryButtonClick() {
    this.secondaryClick.emit();
  }

  applyFocusTrapInDialog() {
    this.nonModalNodes = [];
    this.getAllTabbableElements().forEach(element => {
      if (!this.elemRef.nativeElement.contains(element)) {
        const prevTabIndex = element.getAttribute('tabindex');
        element.setAttribute('tabindex', '-1');
        (element as HTMLElement).style.outline = 'none';
        this.nonModalNodes.push({ element: element, prevTabIndex: prevTabIndex });
      }
    });
  }

  removeFocusTrapFromDialog() {
    for (let i = 0, l = this.nonModalNodes.length; i < l; i++) {
      const node = this.nonModalNodes[i];
      if (node.prevTabIndex) {
        node.element.setAttribute('tabindex', node.prevTabIndex);
        node.prevTabIndex = null;
      } else {
        node.element.removeAttribute('tabindex');
      }
      (node.element as HTMLElement).style.outline = null;
    }
  }

  private getAllTabbableElements(): NodeListOf<Element> {
    return document.querySelectorAll(
      `body
        a[href]:not([tabindex="-1"]),
        area[href]:not([tabindex="-1"]),
        input:not([tabindex="-1"]),
        select:not([tabindex="-1"]),
        textarea:not([tabindex="-1"]),
        button:not([tabindex="-1"]),
        iframe:not([tabindex="-1"]),
        object:not([tabindex="-1"]),
        embed:not([tabindex="-1"]),
        [tabindex]:not([tabindex="-1"]),
        [contenteditable]:not([tabindex="-1"])`
    );
  }
}
