import {
  Component,
  ContentChild,
  HostBinding,
  Input,
  OnInit,
  ElementRef,
  AfterContentInit,
  isDevMode
} from '@angular/core';
import { ContextMenuComponent, ContextMenuEventProviderDirective } from '../context-menu/context-menu.module';
import { SelectedItemInfo } from '../context-menu/context-menu-event-provider.directive';

@Component({
  selector: 'bmw-menu-button',
  templateUrl: './menu-button.component.html',
  styleUrls: ['./menu-button.component.less', '../button/button.component.less']
})
export class MenuButtonComponent implements OnInit, AfterContentInit {
  @ContentChild(ContextMenuComponent)
  childContextMenu: ContextMenuComponent;
  @ContentChild(ContextMenuEventProviderDirective)
  eventProvider: ContextMenuEventProviderDirective;
  @Input()
  disabled: boolean;
  @Input()
  closeOnFocusout: boolean;
  @Input()
  closeOnClickOutside: boolean;
  @Input()
  styleVariant: 'default' | 'primary' | 'secondary' = 'default';
  @Input()
  sizeVariant: 'normal' | 'small' = 'normal';
  @Input()
  preserveState = false;

  @HostBinding('attr.id')
  @Input()
  id: string;

  outsideCallback: any;

  constructor(private elemRef: ElementRef) {}

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }

    // keep the legacy input "closeOnFocusout"
    if (this.closeOnClickOutside === undefined) {
      if (this.closeOnFocusout !== undefined && isDevMode()) {
        console.warn(
          '<bmw-menu-button> `closeOnFocusout` input is deprecated. Please use `closeOnClickOutside` instead'
        );
      }

      this.closeOnClickOutside = this.closeOnFocusout !== undefined ? this.closeOnFocusout : true;
    }
  }

  ngAfterContentInit(): void {
    this.eventProvider?.itemSelected.subscribe((event: SelectedItemInfo) => {
      if (event.leafItem) {
        this.toggle(false);
      }
    });

    this.childContextMenu.closeOnClickOutside = this.closeOnClickOutside;
  }

  toggle(state?: boolean) {
    if (!this.childContextMenu || !this.childContextMenu.toggle) return;
    this.childContextMenu.toggle(state);
    if (!this.preserveState && !this.childContextMenu.opened) this.childContextMenu.unselectSubs();

    document.removeEventListener('click', this.outsideCallback);
    if (this.opened) {
      this.outsideCallback = this.onClickOutside.bind(this);
      document.addEventListener('click', this.outsideCallback);
    }
  }

  onClickOutside({ target }) {
    if (this.closeOnClickOutside && !this.elemRef.nativeElement.contains(target)) {
      this.toggle(false);
      document.removeEventListener('click', this.outsideCallback);
    }
  }

  @HostBinding('class.opened')
  get opened() {
    return this.childContextMenu ? this.childContextMenu.opened : false;
  }

  public click(event: MouseEvent) {
    event.stopPropagation();
    this.toggle();
  }
}
