import { Component, ViewChild, AfterViewInit, Output, EventEmitter, ElementRef, Input } from '@angular/core';

@Component({
  selector: 'bmw-card-tab-label',
  templateUrl: './card-tab-label.component.html',
  styleUrls: ['./card-tab-label.component.less']
})
export class CardTabLabelComponent implements AfterViewInit {
  @ViewChild('close')
  closeButton: any;
  @ViewChild('label')
  label: any;
  @Input()
  closable = true;
  @Input()
  for: string;
  @Input()
  selected: boolean;
  @Input()
  disabled: boolean;
  @Output()
  emitClose = new EventEmitter<string>();
  @Output()
  emitClick = new EventEmitter<string>();
  destroyed = false;
  constructor(public elementRef: ElementRef) {}

  ngAfterViewInit() {
    this.closeButton.nativeElement.addEventListener('click', e => {
      this.emitClose.emit(this.for);
      e.stopPropagation();
    });
    this.label.nativeElement.addEventListener('click', e => {
      this.emitClick.emit(this.for);
      e.stopPropagation();
    });
  }
  destroy() {
    this.elementRef.nativeElement.parentElement.removeChild(this.elementRef.nativeElement);
    this.destroyed = true;
  }
}
