import {
  AfterContentChecked,
  AfterContentInit,
  Component,
  ContentChildren,
  EventEmitter,
  HostBinding,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  HostListener,
  ElementRef
} from '@angular/core';
import { PanelComponent } from '../panel/panel.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'bmw-accordion',
  templateUrl: './accordion.component.html',
  styleUrls: ['./accordion.component.less']
})
export class AccordionComponent implements OnInit, OnChanges, AfterContentInit, OnDestroy {
  @ContentChildren(PanelComponent)
  panels: QueryList<PanelComponent>;

  @Input()
  activePanelId: string = null;

  @HostBinding('attr.id')
  @Input()
  id: string;

  @Output()
  activePanelIdChange: EventEmitter<string> = new EventEmitter();

  private subscriptions: Subscription[] = [];

  constructor(private elem: ElementRef) {}

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        const targetedPanel = Array.prototype.slice
          .call(this.elem.nativeElement.querySelectorAll('.panel'))
          .find((panel: EventTarget) => panel === event.target);
        if (targetedPanel) {
          this.setActivePanel(
            this.panels.find(panel => panel.labelsDiv.nativeElement === targetedPanel).id,
            targetedPanel.classList.contains('opened')
          );
        }
        event.stopImmediatePropagation();
        break;
    }
  }

  ngOnInit() {
    if (typeof this.id === 'undefined') {
      this.id = this.createId();
    }
  }

  ngOnChanges() {
    if (!this.panels) return;
    this.setActivePanel(this.activePanelId);
  }

  ngAfterContentInit(): void {
    this.panels.changes.subscribe(this.setupPanels.bind(this));
    this.setupPanels();
  }

  ngOnDestroy() {
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
  }

  private setupPanels() {
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
    this.subscriptions = this.panels.map((panel: PanelComponent) => {
      return panel.toggleEvent.subscribe(event => {
        this.setActivePanel(panel.id, event.opened);
      });
    });
    if (this.activePanelId) {
      return this.setActivePanel(this.activePanelId);
    }
  }

  private setActivePanel(activePanelId: string, opened = true) {
    this.panels.forEach((panel: PanelComponent) => {
      if (panel.id !== activePanelId && this.canTogglePanel(panel)) {
        panel.opened = false;
      }
      if (panel.id === activePanelId) {
        panel.opened = opened;
        this.activePanelId = opened ? activePanelId : null;
        this.activePanelIdChange.emit(this.activePanelId);
      }
    });
  }

  private canTogglePanel(panel: PanelComponent): boolean {
    return !panel.disabled && !panel.nonCollapsible;
  }

  private createId(): string {
    return Math.random()
      .toString(36)
      .substring(2);
  }
}
