import { Component, ElementRef, HostBinding, HostListener, Input, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';

@Component({
  selector: 'bmw-bottom-navigation-item',
  templateUrl: './bottom-navigation-item.component.html',
  styleUrls: ['./bottom-navigation-item.component.less']
})
export class BottomNavigationItemComponent implements OnInit {
  constructor(private el: ElementRef) {}
  private _selected = false;

  @Input()
  icon: string;

  @HostBinding('title')
  @Input()
  label: string;

  @HostBinding('class.selected')
  @Input()
  set selected(value: boolean) {
    if (value === this._selected) return;

    this._selected = value;
    if (this._selected) {
      this.selectedChange.next(this._selected);
    }
  }
  get selected(): boolean {
    return this._selected;
  }

  @HostBinding('attr.id')
  @Input()
  id: string;

  @HostBinding('tabindex')
  tabIndex = 0;

  @Output()
  selectedChange = new Subject<boolean>();

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  @HostListener('click')
  click() {
    this._selected = !this._selected;
    this.selectedChange.next(this._selected);
  }

  get nativeElement() {
    return this.el.nativeElement;
  }
}
