import { Component, Input, Output, EventEmitter, HostBinding, ViewChild, ElementRef, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-combo-box-dropdown-item',
  templateUrl: './combo-box-dropdown-item.component.html',
  styleUrls: ['./combo-box-dropdown-item.component.less']
})
export class ComboBoxDropdownItemComponent implements OnChanges {
  @Input()
  value: any;
  @Input()
  label: string;
  @Input()
  disabled = false;
  @Input()
  selected = false;
  @Input()
  focused = false;

  @ViewChild('itemWrapper')
  itemWrapper: ElementRef;

  @HostBinding('attr.id')
  @Input()
  id = Math.random()
    .toString(36)
    .substring(2);

  @Output()
  clickEvent = new EventEmitter();

  ngOnChanges(): void {
    if (this.focused) {
      this.itemWrapper?.nativeElement.focus();
    }
  }

  click() {
    if (!this.disabled) this.clickEvent.emit();
  }
}
