import { Component, Input, OnInit, Output, EventEmitter, ElementRef, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.less']
})
export class ButtonComponent implements OnInit, OnChanges {
  @Input()
  button: string;
  @Input()
  iconRight: string;
  @Input()
  iconLeft: string;
  @Input()
  type: string;
  @Input()
  disabled: boolean;
  @Input()
  id: string;
  @Input()
  tabIndex: number;
  @Output()
  clickEvent = new EventEmitter<ButtonComponent>();
  primary = false;
  secondary = false;
  small = false;
  private supportedTypes = ['submit', 'reset', 'button'];

  constructor(public elementRef: ElementRef) {}

  ngOnInit() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);
    this.checkNxProperties();
  }

  ngOnChanges() {
    this.checkNxProperties();
  }

  checkNxProperties() {
    if (this.button) {
      const properties = this.button.split(' ');
      if (properties.indexOf('primary') > -1) {
        this.primary = true;
      } else if (properties.indexOf('secondary') > -1) {
        this.secondary = true;
      }
      if (properties.indexOf('small') > -1) {
        this.small = true;
      }
    }
    if (this.type && !this.supportedTypes.includes(this.type)) {
      console.warn('Wrong button type!', this);
      this.type = 'button';
    }
    if (this.iconLeft) {
      this.iconRight = undefined;
    }

    if (this.disabled) {
      this.elementRef.nativeElement.classList.add('disabled');
    } else {
      this.elementRef.nativeElement.classList.remove('disabled');
    }
  }

  click() {
    if (this.disabled) return;
    this.clickEvent.emit(this);
  }
}
