import { Component, Input, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ToggleButtonComponent } from './toggle-button.component';

@Component({
  selector: 'bmw-toggle-icon-button',
  templateUrl: './toggle-icon-button.component.html',
  styleUrls: ['./toggle-icon-button.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ToggleIconButtonComponent),
      multi: true
    }
  ]
})
export class ToggleIconButtonComponent extends ToggleButtonComponent {
  @Input()
  iconClass = '';
  @Input()
  iconButton: string;
  @Input()
  ariaLabel: string;
  @Input()
  tabIndex = 0;
  constructor() {
    super();
  }

  checkProperties() {
    if (!this.iconClass) throw Error('You have to provide icon class!');

    if (!this.ariaLabel) {
      this.ariaLabel = 'toggle icon button with ' + this.iconClass;
    }

    this.small = this.iconButton === 'small';

    if (this.disabled) {
      this.form.controls[this.name].disable();
    } else {
      this.form.controls[this.name].enable();
    }
  }
}
