import {
  Component,
  OnInit,
  ElementRef,
  Input,
  ContentChild,
  ViewChild,
  OnChanges,
  HostBinding,
  Output,
  EventEmitter,
  HostListener,
  ViewContainerRef,
  TemplateRef,
  AfterViewInit
} from '@angular/core';
import { PanelContentComponent } from './panel-content.component';
import { IconButtonComponent } from '../icon-button/icon-button.component';

@Component({
  selector: 'bmw-panel',
  templateUrl: './panel.component.html',
  styleUrls: ['./panel.component.less'],
  entryComponents: [IconButtonComponent]
})
export class PanelComponent implements OnInit, AfterViewInit, OnChanges {
  @ContentChild(PanelContentComponent)
  childPanelContentComponent: PanelContentComponent;
  @ViewChild('tpl')
  icons: TemplateRef<any>;
  @ViewChild('panel')
  labelsDiv: ElementRef;
  @ViewChild('threeDots')
  threeDots: ElementRef;
  @ViewChild('labelWrapper')
  labelWrapper: ElementRef;
  @ViewChild('hiddenIconsContainer', { read: ViewContainerRef })
  hiddenIconsContainer: ViewContainerRef;
  @ViewChild('visibleIconsContainer', { read: ViewContainerRef })
  visibleIconsContainer: ViewContainerRef;
  @Input()
  disabled = false;
  @Input()
  opened = false;
  @Input()
  nonCollapsible = false;
  @HostBinding('attr.id')
  @Input()
  id: string;

  @Output()
  toggleEvent = new EventEmitter<any>();
  overflowing = false;
  overflowingMenuState = false;
  @HostListener('window:resize')
  onResize() {
    this.resizer();
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        if (event.target === this.labelsDiv.nativeElement) {
          this.toggle();
          event.preventDefault();
          event.stopPropagation();
        }
        break;
    }
  }

  constructor() {}

  ngOnInit() {
    this.checkProperties();
  }

  ngOnChanges() {
    this.checkProperties();
  }

  ngAfterViewInit() {
    this.visibleIconsContainer.insert(this.icons.createEmbeddedView(null));
    setTimeout(() => {
      this.hideIconsIfNecessary();
    });
  }

  checkProperties() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }

    if (this.nonCollapsible === true) {
      this.opened = true;
    }
  }

  closeMenu(e: MouseEvent) {
    const leavingParent = !this.threeDots.nativeElement.contains(<Node>e.relatedTarget);
    if (leavingParent) {
      this.toggleOveflowingMenu(e, false);
    }
  }

  resizer() {
    let resizeTimer: number;

    clearTimeout(resizeTimer);
    resizeTimer = setTimeout(() => {
      this.hideIconsIfNecessary(true);
    });
  }

  hideIconsIfNecessary(showAllFirst?: boolean) {
    if (!this.labelsDiv) return;
    if (showAllFirst) {
      this.showAllIcons();
      this.overflowing = false;
    }
    const defaultHeight = this.labelsDiv.nativeElement.offsetHeight;
    const height = this.labelWrapper.nativeElement.offsetHeight;
    if (height > defaultHeight) {
      this.overflowing = true;
      const id = this.hideLastIcon();
      if (id) {
        setTimeout(() => {
          this.hideIconsIfNecessary();
        });
      }
    }
  }

  showAllIcons() {
    while (this.hiddenIconsContainer.length > 0) {
      this.visibleIconsContainer.insert(this.hiddenIconsContainer.detach(0));
    }
  }

  hideLastIcon(): boolean {
    const length = this.visibleIconsContainer.length;
    this.hiddenIconsContainer.insert(this.visibleIconsContainer.detach(length - 1), 0);

    return length > 1;
  }

  toggle(state?: boolean) {
    if (this.nonCollapsible || this.disabled) return;

    if (typeof state !== 'undefined') {
      this.opened = state;
    } else {
      this.opened = !this.opened;
    }

    this.toggleEvent.emit({
      id: this.id,
      opened: this.opened
    });
  }

  toggleOveflowingMenu(e: MouseEvent, state?: boolean) {
    if (this.disabled) return;

    if (typeof state !== 'undefined') {
      this.overflowingMenuState = state;
    } else {
      this.overflowingMenuState = !this.overflowingMenuState;
    }

    e.stopPropagation();
  }
}
