import { Component, AfterViewInit, Input, ViewChild, ElementRef, Self, Optional, HostBinding } from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';

export const enum LabelPosition {
  Right = 'right',
  Left = 'left'
}
@Component({
  selector: 'bmw-switch',
  templateUrl: './switch.component.html',
  styleUrls: ['./switch.component.less']
})
export class SwitchComponent implements AfterViewInit, ControlValueAccessor {
  @ViewChild('input')
  input: ElementRef;
  @Input()
  name: string;
  @Input()
  required = false;
  @Input()
  id: string;

  @Input()
  labelPosition: 'right' | 'left' | LabelPosition;

  @HostBinding('class.disabled')
  @Input()
  disabled = false;

  @HostBinding('attr.checked')
  @Input()
  checked = false;

  invalid: boolean;
  private isDirty = false;
  private isTouched = false;

  private onChangeCallback: (value: any) => void = () => {};
  private onTouchedCallback: () => void = () => {};

  constructor(@Self() @Optional() private control: NgControl, public elementRef: ElementRef) {
    if (this.control) {
      this.control.valueAccessor = this;
    }
  }

  writeValue(value: boolean): void {
    this.checked = value;
  }

  registerOnChange(fn: (_: boolean) => void): void {
    this.onChangeCallback = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouchedCallback = fn;
  }

  setDisabledState?(disable: boolean): void {
    this.disabled = disable;
  }

  ngAfterViewInit() {
    Promise.resolve().then(() => {
      if (!this.id)
        this.id = Math.random()
          .toString(36)
          .substring(2);
      if (!this.name) this.name = 'switch-' + this.id;
    });
  }

  onTouched() {
    this.onTouchedCallback();
  }

  toggle(event: any) {
    if (this.disabled) return;

    const checkedValue = event.target.checked;

    this.onChangeCallback(checkedValue);

    this.onTouched();

    this.isDirty = true;
    this.isTouched = true;

    if (this.control?.valueAccessor) {
      const { touched, dirty, invalid } = this.control;
      this.invalid = invalid ? dirty || touched : false;
    } else {
      this.invalid = (this.isDirty || this.isTouched) && this.required === true && this.checked === false;
    }
  }
}
