import {
  AfterViewInit,
  Component,
  ContentChild,
  HostBinding,
  Input,
  OnInit,
  HostListener,
  AfterContentInit,
  ViewChild,
  ElementRef
} from '@angular/core';
import { FieldsetHeaderComponent } from './fieldset-header.component';

@Component({
  selector: 'bmw-fieldset',
  templateUrl: './fieldset.component.html',
  styleUrls: ['./fieldset.component.less']
})
export class FieldsetComponent implements OnInit, AfterContentInit, AfterViewInit {
  @ContentChild(FieldsetHeaderComponent)
  header: FieldsetHeaderComponent;

  @ViewChild('label')
  label: ElementRef;

  private _disabled = false;
  get disabled(): boolean {
    return this._disabled;
  }

  @Input()
  set disabled(value: boolean) {
    this._disabled = value;
    if (!this.header) return;
    this.header.disabled = value;
  }

  @HostBinding('attr.id')
  @Input()
  id: string;

  private _opened = false;
  get opened(): boolean {
    return this._opened;
  }

  @Input()
  set opened(value: boolean) {
    this._opened = value;
    if (!this.header) return;
    this.header.opened = value;
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'Enter':
      case 'NumpadEnter':
      case 13:
      case 'Space':
      case 32:
        if (!this.disabled && event.target === this.label.nativeElement) {
          this.toggle();
          event.preventDefault();
          event.stopPropagation();
        }
        break;
    }
  }

  ngOnInit() {
    if (this.header) {
      this.header.opened = this.opened;
    }

    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  ngAfterViewInit() {
    if (this.header) {
      this.header.clickEmit.emit = () => {
        this.toggle();
      };
    }
  }

  ngAfterContentInit() {
    if (this.header) {
      this.header.opened = this.opened;
      this.header.disabled = this.disabled;
    }
  }

  toggle() {
    if (this.disabled) return;
    this.opened = !this.opened;
    if (!this.header) return;
    this.header.opened = this.opened;
  }
}
