import { Component, OnInit, Input, ElementRef, Output, EventEmitter, OnChanges } from '@angular/core';

@Component({
  selector: 'bmw-icon-button',
  templateUrl: './icon-button.component.html',
  styleUrls: ['./icon-button.component.less']
})
export class IconButtonComponent implements OnInit, OnChanges {
  @Input()
  iconClass = '';
  @Input()
  button: string;
  @Input()
  ariaLabel: string;
  @Input()
  disabled = false;
  @Input()
  id: string;
  @Input()
  type: string;
  @Input()
  tabIndex: number;
  @Output()
  clickEvent = new EventEmitter<IconButtonComponent>();
  small = false;
  private supportedTypes = ['submit', 'reset', 'button'];

  constructor(public elementRef: ElementRef) {}

  ngOnInit() {}

  ngOnChanges() {
    this.checkProperties();
  }

  checkProperties() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);

    if (!this.ariaLabel) {
      this.ariaLabel = 'icon button with ' + this.iconClass;
    }

    if (this.button === 'small') {
      this.small = true;
    }
    if (this.type && !this.supportedTypes.includes(this.type)) {
      console.warn('Wrong button type!', this);
      this.type = 'button';
    }
    if (this.disabled) {
      this.elementRef.nativeElement.classList.add('disabled');
    } else {
      this.elementRef.nativeElement.classList.remove('disabled');
    }
  }

  click(event: MouseEvent) {
    this.clickEvent.emit(this);
    event.stopPropagation();
  }
}
