import {
  Component,
  AfterContentInit,
  ContentChildren,
  QueryList,
  HostListener,
  ViewChild,
  ElementRef,
  HostBinding,
  Input,
  isDevMode
} from '@angular/core';

interface InnerButtonComponent {
  button: IconButtonComponent;
  index: number;
}

import { IconButtonComponent } from '../icon-button/icon-button.component';
@Component({
  selector: 'bmw-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.less']
})
export class ToolbarComponent implements AfterContentInit {
  @ViewChild('wrapper')
  wrapper: ElementRef;
  @ViewChild('iconsWrapper')
  iconsWrapper: ElementRef;
  @ContentChildren(IconButtonComponent)
  icons: QueryList<IconButtonComponent>;
  @HostBinding('attr.id')
  @Input()
  id: string;
  visibleIcons: InnerButtonComponent[] = [];
  hiddenIcons: InnerButtonComponent[] = [];
  opened = false;
  resizeTimer: number;
  @HostListener('window:resize')
  listenerFunction(): void {
    //wait at least 10mils between re-render
    clearTimeout(this.resizeTimer);
    this.resizeTimer = window.setTimeout(() => {
      this.onResize(true);
    }, 10);
  }

  constructor() {
    if (isDevMode()) {
      console.warn('Warning - <bmw-toolbar> component is deprecated and will be removed in the near future');
    }
  }

  ngAfterContentInit() {
    if (!this.id)
      this.id = Math.random()
        .toString(36)
        .substring(2);

    this.icons.forEach((icon, index) => {
      this.visibleIcons.push({
        button: icon,
        index: index
      });
    });
    this.onResize();
  }

  iconClick(icon: IconButtonComponent) {
    icon.clickEvent.emit(icon);
  }

  onResize(showAll?: boolean) {
    if (showAll) {
      this.showAll();
    }
    //wait till DOM re-render
    setTimeout(() => {
      const _iconsWrapperHeight = parseInt(this.iconsWrapper.nativeElement.getBoundingClientRect().height, 10);
      const _componentHeight = parseInt(this.wrapper.nativeElement.getBoundingClientRect().height, 10);
      if (_componentHeight < _iconsWrapperHeight) {
        this.hide();
        //wait till DOM re-render
        setTimeout(() => {
          this.onResize();
        });
      }
    });
  }

  hide() {
    const icon = this.visibleIcons.splice(-1, 1);
    this.hiddenIcons = [...icon, ...this.hiddenIcons];
  }

  showAll(): void {
    this.visibleIcons = [...this.visibleIcons, ...this.hiddenIcons];
    this.hiddenIcons = [];
  }

  toggleMenu() {
    this.opened = !this.opened;
  }
}
