import {
  AfterContentInit,
  Component,
  ContentChildren,
  ContentChild,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  AfterViewInit,
  Self,
  Optional,
  ChangeDetectorRef,
  SimpleChanges
} from '@angular/core';
import { ComboBoxDropdownItemComponent } from './combo-box-dropdown-item.component';
import { ComboBoxInputComponent } from './combo-box-input.component';
import { LabelComponent } from '../label/label.component';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { fromEvent, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';

export const enum ComboBoxType {
  SINGLE = 'SINGLE',
  MULTI = 'MULTI'
}

export interface ComboBoxItem {
  value: any;
  label: string;
  disabled: boolean;
  focused?: boolean;
  selected: boolean;
  id: string;
}

export type ComboBoxFilter = (searchQuery: string, allComboBoxItems: ComboBoxItem[]) => ComboBoxItem[];

@Component({
  selector: 'bmw-combo-box',
  templateUrl: './combo-box.component.html',
  styleUrls: ['./combo-box.component.less']
})
export class ComboBoxComponent
  implements OnInit, AfterContentInit, AfterViewInit, ControlValueAccessor, OnChanges, OnDestroy {
  @Input()
  placeholder: string;

  @Input()
  noResultsPlaceholder = 'No results found';

  @Input()
  disabled = false;

  @Input()
  tabIndex = 0;

  @Input()
  required = false;

  @Input()
  clearable = true;

  @Input()
  customFilter: ComboBoxFilter;

  /**
   * Show the multiComboBoxText string instead of each tag. (default = false)
   */
  @Input()
  textInsteadOfTags: boolean;

  @HostBinding('attr.id')
  @Input()
  id: string;

  @HostBinding('attr.tabindex')
  hostTabIndex = '-1';

  @ContentChildren(ComboBoxDropdownItemComponent)
  items: QueryList<ComboBoxDropdownItemComponent>;

  @ContentChild(LabelComponent, { static: true })
  private _label: LabelComponent;

  filteredItems: ComboBoxItem[] = [];

  _items: ComboBoxItem[] = [];
  selectedItems: ComboBoxItem[] = [];

  @ViewChild(ComboBoxInputComponent)
  input: ComboBoxInputComponent;

  @ViewChild('inputBox')
  inputBox: ElementRef;

  opened = false;
  showInvalidState = false;
  comboFocused = false;
  gridClasses: string[];

  private _value: any;
  private _type = ComboBoxType.SINGLE;
  private _inputFocused = false;
  private _isTouched = false;
  public multiComboBoxText = 'Selected';
  public scrollOffset = 0;

  private onDestroy$ = new Subject();
  outsideCallback: any;

  get value(): any {
    return this._value;
  }

  @Input()
  set value(newValue: any) {
    this.changeValue(newValue, { emitEvent: true });
  }

  get useMulti(): boolean {
    return this._type === ComboBoxType.MULTI;
  }

  @Input()
  set type(comboBoxType: ComboBoxType) {
    // coerce inputType
    if (this._type !== comboBoxType) {
      this._type = comboBoxType;
    }
  }

  @Output()
  changeEvent = new EventEmitter<any>();

  @Output()
  open = new EventEmitter<any>();

  /**
   * Text to be used in multi combo boxes that use text instead of tags.
   * It can be a string or a callback function that takes the array of selected items and returns the text in a custom format.
   * In case of providing a string, it replaces the text of the default format: 'Selected (0)'.
   */
  @Input()
  selectedOptionsText: string | ((items: ComboBoxItem[]) => string) = items => 'Selected (' + items.length + ')';

  private _onChange: (value: any) => void = () => {};
  private _onTouched: () => any = () => {};

  private _defaultFilter: ComboBoxFilter = (searchQuery, allComboBoxItems) =>
    allComboBoxItems.filter(({ label }) =>
      label
        ?.toString()
        .toLocaleLowerCase()
        .includes(this.input.value.toLocaleLowerCase())
        ? true
        : false
    );

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;
    switch (key) {
      case 'ArrowDown':
      case 40:
        if (this.opened) {
          this.setNextItemAsFocused();
          this.cdRef.detectChanges();
          this.showInvalidState = false;
        } else {
          this.toggle();
        }
        event.preventDefault();
        break;

      case 'ArrowUp':
      case 38:
        if (this.opened) {
          this.setPreviousItemAsFocused();
          this.cdRef.detectChanges();
          this.showInvalidState = false;
        } else {
          this.toggle();
        }
        event.preventDefault();
        break;

      case 'Backspace':
      case 8:
      case 'Delete':
      case 46:
        if (this.useMulti && this.selectedItems.length > 0 && !this.comboFocused) {
          const focusedTag: ComboBoxItem = this.selectedItems.find(item => item.focused === true);
          if (focusedTag) {
            this.toggleMultiModeItem(focusedTag);
            this.inputBox.nativeElement.querySelector('input').focus();
          }
        }
        break;

      case 'Enter':
      case 'NumpadEnter':
      case 13:
        if (event.target === this.inputBox.nativeElement.querySelector('.clear.iwp-icon-gen_close')) {
          this.clear();
        } else if (this.opened && this.filteredItems.length > 0) {
          const isOnlyItemLeft: boolean = this.filteredItems.length === 1;
          let itemToSelect: ComboBoxItem;

          if (isOnlyItemLeft) {
            itemToSelect = this.filteredItems[0];
          } else {
            itemToSelect = this.filteredItems.find(item => item.focused === true);
          }

          if (itemToSelect && !itemToSelect.disabled) {
            if (!this.useMulti) {
              this.selectItem(this.items.find(item => item.value === itemToSelect.value));
              this.resetFocusedItem();
              this.inputBox.nativeElement.querySelector('input').blur();
            } else {
              this.toggleMultiModeItem(itemToSelect, { removeFocusAfterSelection: false });
              if (isOnlyItemLeft) {
                this.input.value = '';
                this.cleanFilter();
              }
            }
          }
        }
        event.stopPropagation();
        event.preventDefault();
        break;

      case 'Space':
      case 32:
        if (!this.opened || !this.input.value) {
          this.toggle();
          event.returnValue = false;
          this.inputBox.nativeElement.querySelector('input').focus();
        }

        break;

      case 'Escape':
      case 27:
      case 'Tab':
      case 9:
        if (this.opened) {
          this.toggle(false);
        }

        this.updateViewInputAfterDropdownClosed();

        if (!this.filteredItems.find(item => item.focused === true)) {
          this.comboFocused = false;
        }
        break;
    }
  }

  @HostListener('focusout', ['$event'])
  onFocusout({ target }) {
    if (target === this.inputBox.nativeElement.querySelector('input') && !this.opened) {
      this.updateViewInputAfterDropdownClosed();
    }
  }

  constructor(
    @Self() @Optional() private control: NgControl,
    private elementRef: ElementRef,
    private cdRef: ChangeDetectorRef
  ) {
    if (this.control) this.control.valueAccessor = this;
  }

  ngOnInit() {
    this._listenToInputFocus();

    this.control?.valueChanges.pipe(takeUntil(this.onDestroy$)).subscribe(e => {
      this._validate();
    });

    this.elementRef.nativeElement.focus = () => {
      this.focusInput();
      this.toggle(true);
    };

    this.updateMultiComboBoxText();
  }

  ngAfterContentInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }

    this.filter();
    this._validate();

    this.items.changes.pipe(takeUntil(this.onDestroy$)).subscribe(() => {
      this.mapItems();
      this.updateViewInput();
      this.cleanFilter();
    });

    this.refreshItemsState();

    if (this._label) {
      this._label.inputId = this.id;
    }
    this._setGridClasses();
  }

  ngAfterViewInit() {
    if (this._value) {
      // Initial value was set, update selected items
      this.updateViewInput();
      this.cleanFilter();
      this.cdRef.detectChanges();
    }
  }

  mapItems() {
    this._items = this.items.map(item => {
      return {
        value: item.value,
        label: item.label,
        disabled: item.disabled,
        focused: item.focused,
        selected: item.selected,
        id: item.id
      };
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.selectedOptionsText) {
      this.updateMultiComboBoxText();
    }
    this.filter();
    this._validate();
  }

  ngOnDestroy(): void {
    this.cdRef.detach();
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

  focusTag(selectedItem: ComboBoxItem) {
    selectedItem.focused = true;
  }

  focusoutTag(selectedItem: ComboBoxItem) {
    selectedItem.focused = false;
  }

  selectTag(selectedItem: ComboBoxItem) {
    if (selectedItem) {
      // remove selection from all tags
      this.selectedItems = this.selectedItems.map(item => {
        item.focused = false;
        return item;
      });
      this.updateMultiComboBoxText();
    }
  }

  toggle(state?: boolean) {
    if (!this.disabled) {
      const initialOpenedState = this.opened;

      if (typeof state !== 'undefined') {
        this.opened = state;
      } else {
        this.opened = !this.opened;
      }

      if (this.opened !== initialOpenedState) {
        if (this.opened) {
          this.outsideCallback = this.onClickOutside.bind(this);
          document.addEventListener('mouseup', this.outsideCallback);
          document.addEventListener('touchend', this.outsideCallback);
          this.inputBox.nativeElement.querySelector('input').focus();
          this.open.emit();
        } else {
          this.resetFocusedItem();
          document.removeEventListener('mouseup', this.outsideCallback);
          document.removeEventListener('touchend', this.outsideCallback);

          this.comboFocused = this._inputFocused;
        }
      }
    }
  }

  onClickOutside({ target }) {
    if (
      !this.elementRef.nativeElement.querySelector('.combo-box-container').contains(target) &&
      !target.shadowRoot?.querySelector('bmw-combo-box')
    ) {
      this.toggle(false);
      this.updateViewInputAfterDropdownClosed();
    }
  }

  refreshItemsState() {
    this.mapItems();
    this.filteredItems = [...this._items];
  }

  resetFocusedItem() {
    this.filteredItems.forEach(item => (item.focused = false));
  }

  setPreviousItemAsFocused() {
    if (this.opened && this.filteredItems.length > 0 && this.filteredItems.some(item => !item.disabled)) {
      let focusedItemIndex: number = this.filteredItems.findIndex(item => item.focused === true);
      this.resetFocusedItem();
      if (focusedItemIndex !== -1 && focusedItemIndex !== 0) {
        focusedItemIndex = focusedItemIndex - 1;
      } else {
        focusedItemIndex = this.filteredItems.length - 1;
      }

      this.filteredItems[focusedItemIndex].focused = true;

      if (this.filteredItems[focusedItemIndex].disabled) {
        this.setPreviousItemAsFocused();
      }
    }
  }

  setNextItemAsFocused() {
    if (this.opened && this.filteredItems.length > 0 && this.filteredItems.filter(item => !item.disabled).length > 0) {
      let focusedItemIndex: number = this.filteredItems.findIndex(item => item.focused === true);
      this.resetFocusedItem();
      if (focusedItemIndex !== -1 && focusedItemIndex !== this.filteredItems.length - 1) {
        focusedItemIndex = focusedItemIndex + 1;
      } else {
        focusedItemIndex = 0;
      }

      this.filteredItems[focusedItemIndex].focused = true;

      if (this.filteredItems[focusedItemIndex].disabled) {
        this.setNextItemAsFocused();
      }
    }
  }

  toggleMultiModeItem(item: ComboBoxItem, options?: { removeFocusAfterSelection: boolean }) {
    item.selected = !item.selected;

    this.updateSelectedItems(item);

    if (!options || options.removeFocusAfterSelection) {
      const currentlyFocusedIndex = this.filteredItems.findIndex(searchedItem => !!searchedItem.focused);
      if (currentlyFocusedIndex > 0) {
        this.filteredItems[currentlyFocusedIndex].focused = false;
      }
    }

    const itemInDropdownList = this.filteredItems.find(dropdownItem => dropdownItem.value === item.value);

    itemInDropdownList.selected = item.selected;

    this.findFocusedTag();
    this.emitSelectedItemsChange();
    this._validate();
  }

  updateSelectedItems(item: ComboBoxItem) {
    if (item.selected) {
      this.selectedItems.push(item);
    } else {
      this.selectedItems = this.selectedItems.filter(function(element: ComboBoxItem) {
        return element.value !== item.value;
      });
    }
    this.updateMultiComboBoxText();
  }

  updateViewInput(): void {
    if (Array.isArray(this._value) || this.useMulti) {
      this._items = this._items.map(item => {
        item.selected = (this._value as any[])?.includes(item.value);
        return item;
      });

      this.selectedItems = this._items.filter(item => item.selected);
      this.updateMultiComboBoxText();
    } else {
      if (this._value === null) {
        this.resetDropdownItems();
      }

      const selectedItem = this.findItemByValue(this._value);

      if (this.input) {
        this.input.value = selectedItem?.label ?? '';
      }
    }
  }

  updateMultiComboBoxText(): void {
    if (this.useMulti) {
      if (typeof this.selectedOptionsText === 'function') {
        this.multiComboBoxText = this.selectedOptionsText(this.selectedItems);
      } else {
        this.multiComboBoxText = this.selectedOptionsText + ' (' + this.selectedItems.length + ')';
      }
    }
  }

  focusFirstFilteredItem() {
    if (this.input && this.opened && this.filteredItems.length > 0 && this.filteredItems.some(item => !item.disabled)) {
      const enabledItems = this.filteredItems.filter(item => !item.disabled);
      enabledItems[0].focused = true;
      this.cdRef.detectChanges();
      this.inputBox.nativeElement.querySelector('input').focus();
      this.showInvalidState = false;
    }
  }

  updateViewInputAfterDropdownClosed(): void {
    this.resetFocusedItem();

    if (!this.useMulti && this._value) {
      this.updateViewInput();
    } else {
      this.input.value = '';
    }

    this.cleanFilter();
    this._validate();
  }

  resetDropdownItems(): void {
    this.filteredItems = [...this._items];
    this.selectedItems = [];
    this.updateMultiComboBoxText();
    this._items = this._items.map(item => {
      item.selected = false;
      return item;
    });
  }

  emitSelectedItemsChange(): void {
    const _selectedItems = this._items.filter(item => item.selected).map(item => item.value);
    this.value = _selectedItems;
    this.changeEvent.emit(_selectedItems);
    this._onChange(_selectedItems);
    this._onTouched();
  }

  filter() {
    this.resetFocusedItem();
    if (!this._items) {
      this.filteredItems = [];
      return;
    }
    const filter: ComboBoxFilter = this.customFilter ?? this._defaultFilter;
    this.filteredItems = filter(this.input?.value?.toString(), this._items);
  }

  filterAndOpenDropdown() {
    this.filter();
    this.toggle(true);
    this.focusFirstFilteredItem();
  }

  cleanFilter() {
    if (!this._items) {
      this.filteredItems = [];
    } else {
      this.filteredItems = this._items;
    }
  }

  selectItem(item: ComboBoxDropdownItemComponent) {
    if (this.useMulti) {
      this.toggleMultiModeItem(item);
    } else {
      this.value = item.value;
      this.filteredItems = [...this._items];
      this.toggle(false);
      this.changeEvent.emit(item.value);
      this._onTouched();
      this._validate();
    }
  }

  findItemByValue(value: any): ComboBoxDropdownItemComponent {
    if (!this.items) return null;

    return this.items.filter(item => {
      return item.value === value || JSON.stringify(item.value) === JSON.stringify(value);
    })[0];
  }

  clear() {
    this._value = null;
    this.input.value = null;
    this.changeEvent.emit(null);
    this._onChange(null);
    this._onTouched();
    this._validate();
  }

  isFormReset() {
    let reset = false;

    if (this.control) {
      const { pristine } = this.control;
      reset = this._value === null && pristine !== false;
    }

    return reset;
  }

  registerOnChange(fn: (value: any) => void) {
    this._onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this._onTouched = fn;
  }

  writeValue(value: any): void {
    this.changeValue(value, { emitEvent: false });
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
  }

  private changeValue(newValue: any, options: { emitEvent: boolean }) {
    this._value = newValue;

    this.updateViewInput();

    if (Array.isArray(newValue)) {
      this.findFocusedTag();
    }

    if (options.emitEvent && !this.isFormReset()) {
      this._onChange(newValue);
    }
    this._validate();
  }

  private _validate() {
    if (this.control) {
      const { touched, invalid } = this.control;
      this.showInvalidState = invalid && touched;
    } else {
      if (this.useMulti) {
        this.showInvalidState =
          this._isTouched && this.required === true && this._items.filter(item => item.selected).length === 0;
      } else {
        this.showInvalidState = this._isTouched && this.required === true && !this._value;
      }
    }
  }

  private _listenToInputFocus() {
    if (!this.inputBox) return;
    const inputElement = (this.inputBox.nativeElement as HTMLElement).querySelector('input');
    const focusEvent = fromEvent(inputElement, 'focus').pipe(map(() => 'focus'));
    const blurEvent = fromEvent(inputElement, 'blur').pipe(map(() => 'blur'));

    merge(focusEvent, blurEvent)
      .pipe(
        takeUntil(this.onDestroy$),
        map(eventName => (eventName === 'focus' ? true : false))
      )
      .subscribe(inputFocus => {
        this.comboFocused = inputFocus;
      });
  }

  focusInput() {
    if (!this.disabled) {
      this._inputFocused = true;
      this.comboFocused = true;
    }
  }

  focusoutInput() {
    this._inputFocused = false;
    this.comboFocused = this.opened;
  }

  private _setGridClasses() {
    const listOfClasses = Array.from((this.elementRef.nativeElement as HTMLElement).classList);
    this.gridClasses = listOfClasses.filter((item: string) => item.match(/bmw__grid__cell.*/));
    if (this.gridClasses.length > 0) {
      const wrapperClassList = this.elementRef.nativeElement.querySelector('div.combo-box-container').classList;
      wrapperClassList.value.split(' ').forEach((className: string) => {
        if (className.match(/bmw__grid__cell.*/)) {
          wrapperClassList.remove(className);
        }
      });
      this.gridClasses.forEach((className: string) => {
        this.elementRef.nativeElement.querySelector('div.combo-box-container').classList.add(className);
        this.elementRef.nativeElement.classList.remove(className);
      });
    }
  }

  private findFocusedTag() {
    setTimeout(() => {
      const focused = this.elementRef.nativeElement.querySelector('bmw-tag div.focused');
      if (!focused) {
        this.scrollOffset = 0;
        return;
      }

      const diff =
        focused.parentElement.getBoundingClientRect().x -
        focused.parentElement.parentElement.parentElement.getBoundingClientRect().x -
        this.scrollOffset;
      if (diff < 0) {
        this.scrollOffset = -diff;
      } else {
        this.scrollOffset = 0;
      }
    });
  }

  public click() {
    this.toggle();
  }

  public inputTouched() {
    this._isTouched = true;
    this._onTouched();
  }
}
