import { Injectable, ElementRef } from '@angular/core';
import { TooltipPosition } from './tooltip.enum';

@Injectable({
  providedIn: 'root'
})
export class TooltipPositionService {
  switchPosition(tooltip: ElementRef, position: TooltipPosition): TooltipPosition {
    const tooltipPosition = tooltip.nativeElement.getBoundingClientRect();
    const tooltipOverflowX = tooltipPosition.x < 0;
    const tooltipOverflowY = tooltipPosition.y < 0;

    if (tooltipOverflowX) {
      return position === TooltipPosition.AFTER ? TooltipPosition.BEFORE : TooltipPosition.AFTER;
    }

    if (tooltipOverflowY) {
      return position === TooltipPosition.ABOVE ? TooltipPosition.BELOW : TooltipPosition.ABOVE;
    }

    return position;
  }
}
