import { Component, Input, ElementRef } from '@angular/core';

@Component({
  selector: 'bmw-label',
  templateUrl: './label.component.html',
  styleUrls: ['./label.component.less']
})
export class LabelComponent {
  _inputId: string;

  @Input()
  disabled = false;

  @Input()
  required: boolean;

  @Input()
  set for(newFor: string) {
    this.inputId = newFor;
  }

  set inputId(id: string) {
    this._inputId = id;
  }
  get inputId() {
    return this._inputId;
  }
}
