import { takeUntil } from 'rxjs/operators';
import { NavigationService } from './navigation.service';
import { Component, OnInit, HostBinding, Input, OnDestroy, HostListener, ViewChild, ElementRef } from '@angular/core';
import { Subject } from 'rxjs';

@Component({
  selector: 'bmw-navigation-menu-submenu-item',
  templateUrl: './navigation-menu-submenu-item.component.html',
  styleUrls: ['./navigation-menu-submenu-item.component.less']
})
export class NavigationMenuSubmenuItemComponent implements OnInit, OnDestroy {
  private _selected = false;
  private _disabled = false;
  private _destroyed$ = new Subject();

  @HostBinding('attr.id')
  @Input()
  id: string;
  @Input()
  set disabled(value: boolean) {
    this._disabled = value;
    if (value) this._selected = false;
  }
  get disabled(): boolean {
    return this._disabled;
  }
  @Input()
  set selected(value: boolean) {
    if (this.disabled || value === this._selected) return;

    this._selected = value;
  }
  get selected(): boolean {
    return this._selected;
  }

  @ViewChild('submenuItemWrapper')
  submenuItemWrapper: ElementRef;

  selectedEvent = new Subject<NavigationMenuSubmenuItemComponent>();

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent) {
    const key = event.code || event.keyCode;

    if (['Enter', 13, 'Space', 32].includes(key) && event.target === this.submenuItemWrapper.nativeElement) {
      this.clickEvent(null);
      event.preventDefault();
      event.stopPropagation();
    }
  }
  constructor(private _navService: NavigationService) {}

  ngOnInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }

    this._navService.selectionChange.pipe(takeUntil(this._destroyed$)).subscribe(item => {
      if (item !== this) {
        this.selected = false;
      }
    });
  }

  ngOnDestroy() {
    this._destroyed$.next();
    this._destroyed$.complete();
  }

  clickEvent($event) {
    $event?.stopPropagation();
    if (this.disabled || this.selected) return;
    this._navService.selectItem(this);
    this._selected = true;
    this.selectedEvent.next(this);
  }
}
