import { Component, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'bmw-fieldset-header',
  templateUrl: './fieldset-header.component.html',
  styleUrls: ['./fieldset-header.component.less']
})
export class FieldsetHeaderComponent {
  opened = false;
  disabled: boolean;
  @Output()
  clickEmit = new EventEmitter();

  emitClick() {
    this.clickEmit.emit();
  }
}
