import { Component, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'bmw-tag',
  templateUrl: './tag.component.html',
  styleUrls: ['./tag.component.less']
})
export class TagComponent {
  @Input()
  label: string;
  @Input()
  focused = false;
  @Input()
  disabled = false;
  @Output()
  removeEvent = new EventEmitter<void>();

  cancel() {
    if (!this.disabled) {
      this.removeEvent.emit();
    }
  }
}
