import { Component, HostListener, Input, ViewChild, ElementRef } from '@angular/core';
import { TooltipPosition, TooltipDisplay } from './tooltip.enum';
import { TooltipPositionService } from './tooltip-position.service';

@Component({
  selector: 'bmw-tooltip',
  styleUrls: ['tooltip.component.less'],
  templateUrl: 'tooltip.component.html'
})
export class TooltipComponent {
  @Input()
  theme: string;

  @Input()
  heading: string;

  @Input()
  content: string;

  @Input()
  position = TooltipPosition.BELOW;

  @Input()
  display = TooltipDisplay.INLINE;

  @Input()
  active = false;

  @Input()
  disabled = false;

  @ViewChild('tooltip')
  tooltipRef: ElementRef;

  constructor(private tooltipPositionService: TooltipPositionService) {}

  show() {
    this.active = true;
    // Use setTimeout because tooltip in not present in the DOM to calculate it's position
    setTimeout(() => {
      this.position = this.tooltipPositionService.switchPosition(this.tooltipRef, this.position);
    }, 0);
  }

  hide(): void {
    this.active = false;
  }

  @HostListener('focus')
  @HostListener('focusin')
  @HostListener('mouseenter')
  onMouseEnter() {
    this.show();
  }

  @HostListener('blur')
  @HostListener('focusout')
  @HostListener('mouseleave')
  onMouseLeave() {
    this.hide();
  }
}
