import { Component, HostListener, Output, EventEmitter, HostBinding } from '@angular/core';
/* tslint:disable:component-selector*/
@Component({
  selector: 'input[bmwComboBoxInput]',
  templateUrl: './combo-box-input.component.html',
  styleUrls: ['./combo-box-input.component.less']
})
export class ComboBoxInputComponent {
  @Output()
  clickEvent = new EventEmitter();
  @Output()
  focusEvent = new EventEmitter();
  @Output()
  inputEvent = new EventEmitter();

  @HostBinding('value')
  value = '';

  @HostListener('click')
  onCLick() {
    this.clickEvent.emit();
  }
  @HostListener('focus')
  onFocus() {
    this.focusEvent.emit();
  }
  @HostListener('input', ['$event'])
  onInput(event: any) {
    this.value = event.target.value;
    this.inputEvent.emit();
  }
}
