import {
  Component,
  OnInit,
  Input,
  AfterContentInit,
  Output,
  EventEmitter,
  HostListener,
  ElementRef,
  ViewChild,
  isDevMode,
  OnChanges,
  SimpleChanges
} from '@angular/core';

export declare interface ITableHeader {
  field: string;
  headerName?: string;
  sorted?: boolean;
  sortable?: boolean;
  sort?: 'asc' | 'desc';
}

export declare interface ITableRow {
  [key: string]: string | string[] | number;
}

@Component({
  selector: 'bmw-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.less']
})
export class TableComponent implements OnInit, AfterContentInit, OnChanges {
  @Input()
  columnDefs: ITableHeader[];

  @Input()
  columns: ITableHeader[];

  @Input()
  rowData: ITableRow[];

  @Input()
  data: ITableRow[];

  @Input()
  id: string;

  @Input()
  hideHeader = false;

  @Input()
  zebra = false;

  @Input()
  stickHeader = false;

  @Output()
  tableSorted = new EventEmitter<ITableHeader>();

  @ViewChild('tableContainer')
  tableContainer: ElementRef;

  @ViewChild('headerContainer')
  headerContainer: ElementRef;

  public isLoading = false;

  public rowsAfterSort: ITableRow[];

  constructor() {}

  @HostListener('window:scroll')
  onScroll() {
    if (this.stickHeader) {
      if (
        this.headerContainer.nativeElement.getBoundingClientRect().top <= 0 &&
        this.tableContainer.nativeElement.getBoundingClientRect().bottom > 0 &&
        this.tableContainer.nativeElement.getBoundingClientRect().top < 0
      ) {
        if (this.isIE()) {
          this.headerContainer.nativeElement.classList.add('fixed');
          this.headerContainer.nativeElement.setAttribute(
            'style',
            `
              left: -${window.pageXOffset}px;
            `
          );
        }
      } else {
        if (this.isIE()) {
          this.headerContainer.nativeElement.classList.remove('fixed');
        }
      }

      if (this.isIE()) {
        this.setElementsWidthRecursivelyBecauseHTMLTablesAreHardToDealWith(this.tableContainer.nativeElement);
      }
    }
  }

  shouldHaveStickHeader(): boolean {
    return this.stickHeader && !this.isIE();
  }

  ngOnInit(): void {
    if (this.data?.length) {
      this.rowsAfterSort = this.data.map(i => ({ ...i }));
    }
    // keep the legacy input
    else if (this.rowData?.length) {
      if (isDevMode()) {
        console.warn('<bmw-table> `rowData` input is deprecated. Please use `data` instead');
      }

      this.rowsAfterSort = this.rowData.map(i => ({ ...i }));
    }

    // keep the legacy input
    if (this.columnDefs?.length) {
      if (isDevMode()) {
        console.warn('<bmw-table> `columnDefs` input is deprecated. Please use `columns` instead');
      }
      this.columns = this.columnDefs;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['data']) {
      this.rowsAfterSort = this.data.map(i => ({ ...i }));
    }
    // keep the legacy input
    else if (changes['rowData']) {
      this.rowsAfterSort = this.rowData.map(i => ({ ...i }));
    }
    // keep the legacy input
    else if (changes['columnDefs']) {
      this.columns = this.columnDefs;
    }
  }

  ngAfterContentInit() {
    if (!this.id) {
      this.id = Math.random()
        .toString(36)
        .substring(2);
    }
  }

  applySortToRows(header: ITableHeader): void {
    this.isLoading = true;
    if (header.sort === 'asc') {
      this.rowsAfterSort = this.rowsAfterSort.sort((a, b) =>
        a[header.field] > b[header.field] ? 1 : a[header.field] === b[header.field] ? 0 : -1
      );
    } else if (header.sort === 'desc') {
      this.rowsAfterSort = this.rowsAfterSort.sort((a, b) =>
        a[header.field] < b[header.field] ? 1 : a[header.field] === b[header.field] ? 0 : -1
      );
    }

    this.tableSorted.emit(header);
    this.isLoading = false;
  }

  private toggleSort(header: ITableHeader) {
    if (header.sorted) {
      if (header.sort === null || header.sort === 'asc') {
        header.sort = 'desc';
      } else {
        header.sort = 'asc';
      }
    } else {
      if (!header.sort) {
        header.sort = 'asc';
      }
    }
  }

  sortRowsByProperty(property: string): void {
    if (this.columns?.some(col => col.field === property && col.sortable === true)) {
      this.columns.forEach(header => {
        if (header.sortable && header.field === property) {
          this.toggleSort(header);
          this.applySortToRows(header);
          header.sorted = true;
        } else {
          header.sort = null;
          header.sorted = false;
        }
      });
    }
  }

  private setElementsWidthRecursivelyBecauseHTMLTablesAreHardToDealWith(element) {
    this.setElementInlineWidth(element);

    for (let i = 0; i < element.childNodes.length; i++) {
      this.setElementsWidthRecursivelyBecauseHTMLTablesAreHardToDealWith(element.childNodes[i]);
    }
  }

  private setElementInlineWidth(element) {
    if (element.getBoundingClientRect) {
      const width = element.getBoundingClientRect().width;

      element.setAttribute(
        'style',
        `
    width: ${width}px !important;
    max-width: ${width}px !important;
    `
      );
    }
  }

  private isIE(): boolean {
    let result = false;

    if (window.navigator.userAgent.indexOf('MSIE ') > 0 || !!navigator.userAgent.match(/Trident.*rv\:11\./))
      result = true;

    return result;
  }
}
